// 音乐开关

cc.Class({
    extends: cc.Component,

    properties: {
        Background: cc.Sprite,
        OpenSpriteFrame: cc.SpriteFrame,
        CloseSpriteFrame: cc.SpriteFrame,
    },

    onLoad() {
        this._isopen = Wu57.StorageMgr.isMusicOpen();
        this.changeSpriteFrame();
    },

    //切换开关
    switch() {
        this._isopen = !this._isopen;
        this.changeSpriteFrame();
    },

    //根据开关状态更换图片和进行对应操作
    changeSpriteFrame() {
        let frame = this._isopen ? this.OpenSpriteFrame : this.CloseSpriteFrame;
        this.Background.spriteFrame = frame;
        if(this._isopen) {
            Wu57.AudioMgr.disableMute();
        } else {
            Wu57.AudioMgr.enableMute();
        }
    },

    //音乐开关是否打开
    isOpen() {
        return this._isopen;
    },
});
