// 左

cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        let x = -this.node.width;
        let y = cc.winSize.height - this.node.height * 0.5 - 120;
        this.node.setPosition(x, y);
    },

    onEnter() {
        let offset = cc.v2(this.node.width+4, 0);
        this.node.runAction( cc.moveBy(Wu57.Constant.UIEnterExitTime, offset).easing(cc.easeBackInOut()) );
    },

    onExit() {
        let offset = cc.v2(-this.node.width-4, 0);
        this.node.runAction( cc.moveBy(Wu57.Constant.UIEnterExitTime, offset).easing(cc.easeBackInOut()) );
    },

    // update (dt) {},
});
