
cc.Class({
    extends: cc.Component,

    properties: {
        Notice: cc.Node,
        Container: cc.Node,
        TitleLab: cc.Label,
        TipLab: cc.Label,
        NoticeLab: cc.Label
    },

    onLoad () {
        this.AnimateTime = 0.85;
        this.Pos1 = cc.v2(0, cc.winSize.height * 0.5 + this.Container.height);
        this.Pos2 = cc.v2(0, 0);
        this.MaskTouch = this.Notice.getComponent('MaskTouch');
    },

    //播放通知动画
    playAnimation(staytime=2) {
        this.NoticeLab.node.opacity = 0;
        this.NoticeLab.node.stopAllActions();
        this.NoticeLab.node.runAction(
            cc.sequence(
                cc.fadeIn(0.25),
                cc.delayTime(staytime),
                cc.fadeOut(0.25)
            )
        );
    },

    //关闭提示
    closeTip() {
        this.NoticeLab.node.stopAllActions();
        this.NoticeLab.node.opacity = 0;
    },

    //发送提示通知
    sendTip(text, time=2) {
        this.NoticeLab.string = text;
        // this.NoticeLab.node.color = cc.Color.BLACK;
        this.playAnimation(time);
    },

    //发送警告通知
    sendWarning(text, time=2) {
        this.NoticeLab.string = text;
        // this.NoticeLab.node.color = cc.Color.BLACK.fromHEX('#E227E3');
        this.playAnimation(time);
    },

    //发送错误通知
    sendError(text, time) {
        this.NoticeLab.string = text;
        // this.NoticeLab.node.color = cc.Color.RED;
        this.playAnimation(time);
    },

    //网络问题
    checkNetwork(onConfirm, onCancel) {
        this.open('提示', '您的网络似乎有点问题哦', onConfirm, onCancel);
    },

    //打开通知
    open (title='提示', tip, onConfirm, onCancel) {
        this.MaskTouch.enableTouch();
        this.Container.active = true;
        this.Container.setPosition(this.Pos1);
        this.TitleLab.string = title;
        this.TipLab.string = tip;
        this.onConfirm = onConfirm;
        this.onCancel = onCancel;

        let act = cc.moveTo(this.AnimateTime, this.Pos2).easing(cc.easeBackInOut());
        this.Container.runAction(act);
    },

    //关闭通知
    close () {
        let act = cc.moveTo(this.AnimateTime, this.Pos1).easing(cc.easeBackInOut());
        this.Container.runAction(act);
        this.scheduleOnce(() => {
            this.MaskTouch.disableTouch();
            if(this.onCancel) {
                this.onCancel();
            }
            this.onConfirm = null;
            this.onCancel = null;
            this.Container.active = false;
        }, this.AnimateTime);
    },

    //确认
    confirm () {
        if(this.onConfirm) {
            this.onConfirm();
        }
        this.onConfirm = null;
        this.onCancel = null;
        this.close();
    },
    
});
