
cc.Class({
    extends: cc.Component,

    properties: {
        AutoSaveTime: {
            default: 30,
            tooltip: '间隔同步时间',
            type: cc.Integer
        }
    },

    onLoad() {
        this.AutoRestoreTime = 6 * 60;
        this.onSuccess = null;
        this.onFailed = null;
    },

    onDestroy() {
        this.stopLoop();
    },

    //每隔一段时间同步
    runLoop() {
        this.schedule(this.autoAddEnergy, this.AutoRestoreTime);
        this.schedule(this.autoSave, this.AutoSaveTime);  
    },

    //同步
    stopLoop() {
        this.unschedule(this.autoSave);
        this.unschedule(this.autoAddEnergy);
    },

    //自动回复体力
    autoAddEnergy() {
        this.modEnergy(1);
    },

    // -------------- Basic --------------

    // _User表的 objectId 相当于我们的 userId
    init (userId) {
        this._userId = userId;  //用户ID
        this._first = true;     //第一次初始化
        this._difftime = 0;     //时间差
        this.query();
    },

    //查询当前用户信息
    query() {
        const query = Wu57.Bomb.Query('_User');
        query.get(this._userId).then(res => {
            this._handler = res;
            if(this._first) {
                let backup = Wu57.CipherMgr.encode(Wu57.StorageMgr._getSession());
                res.set('backup', backup);
                res.save();
                this.setLastTime();
                this.runLoop();
            }
            let s1 = this._handler.maintask || '{"list":[]}';
            let s2 = this._handler.timetask || '{}';
            let s3 = this._handler.items || '{}';
            this._handler.maintask_ = JSON.parse(s1);
            this._handler.timetask_ = JSON.parse(s2);
            this._handler.items_ = JSON.parse(s3);
            console.log('[获得用户句柄成功]', res);
        }).catch(res => {
            console.log('[获得用户句柄失败]', res);
        });
    },

    //获得时间差
    getDiffTime() {
        return this._difftime;
    },

    //重置时间差（使用过后需要手动清空）
    resetDiffTime() {
        this._difftime = 0;
    },

    //调整时间差产生的数据
    adjustDiffTime() {
        let tasks = this._handler.timetask_;
        let size = Object.keys(tasks).length;
        if(size > 0) {
            console.log('[调整时间差产生的数据]', tasks);
            let time = this._difftime;
            for(let taskid in tasks) {
                let _time = tasks[taskid].time - time;
                tasks[taskid].time = Math.max(0, _time);
            }
            this._handler.energy += Math.floor(time / this.AutoRestoreTime);
            this._handler.timetask = JSON.stringify(this._handler.timetask_);
            this.commit();
        }
        this.resetDiffTime();
    },

    //缓存服务器时间
    saveLastTime() {
        Wu57.Bomb.timestamp().then(res => {
            console.log('[缓存服务器时间]', res);
            let t2 = res.timestamp;
            this._handler.lasttime = t2;
            this._handler.save();
            this.commit();
        }).catch(res => {
            console.log('[缓存服务器时间]', res);
        })
    },

    //同步服务器时间（返回后可以计算时间差）
    setLastTime() {
        Wu57.Bomb.timestamp().then(res => {
            console.log('[获取服务器时间]', res);
            let t1 = this._handler.lasttime;
            let t2 = res.timestamp;
            if(t2 - t1 > 0) {
                this._difftime = t2 - t1;
                console.log('[时间差]', this._difftime);
                //处理时间差问题
                this.adjustDiffTime();
            }
            this._handler.lasttime = t2;
            this._handler.save();
            if(this._first) {
                Wu57.EventMgr.emit(Wu57.Constant.EventCode.DatabaseLoaded);
            }
            this._first = false;
            this.commit();
        }).catch(res => {
            console.log('[获取服务器时间]', res);
        });
    },

    //提交数据库修改
    commit() {
        const query = Wu57.Bomb.Query('_User');
        query.get(this._userId).then(res => {
            if(this._handler.coin == undefined) {
                this._handler.coin = this._handler.coin || 200;
                this._handler.diamond = this._handler.diamond || 0;
                this._handler.exp = this._handler.exp || 0;
                this._handler.energy = this._handler.energy == undefined ? 100 : this._handler.energy;
                this._handler.bagspace = this._handler.bagspace || 24;
                this._handler.items = this._handler.items || '{}';
                this._handler.maintask = this._handler.maintask || '{"list":[]}';
                this._handler.timetask = this._handler.timetask || '{}';
                this._handler.headurl = this._handler.headurl || '';
            }

            let coin = this._handler.coin;
            let diamond = this._handler.diamond;
            let exp = this._handler.exp;
            let energy = this._handler.energy;
            let bagspace = this._handler.bagspace;
            let items = this._handler.items;
            let maintask = this._handler.maintask;
            let timetask = this._handler.timetask;
            let headurl = this._handler.headurl;
            let lasttime = this._handler.lasttime;
            res.set('bagspace', bagspace);
            res.set('coin', coin);
            res.set('diamond', diamond);
            res.set('exp', exp);
            res.set('energy', energy);
            res.set('items', items);
            res.set('maintask', maintask);
            res.set('timetask', timetask);
            res.set('headurl', headurl);
            res.set('lasttime', lasttime);
            res.save();

            if(this.onSuccess) {
                this.onSuccess(res);
            }
            this.onSuccess = null;
            this.onFailed = null;
            console.log('[提交修改成功]');
        }).catch(res => {
            console.log('[提交修改失败]', res);
            if(this.onFailed) {
                this.onFailed(res);
            }
            this.onSuccess = null;
            this.onFailed = null;
        });
    },

    //自动保存
    autoSave() {
        this._handler.maintask = JSON.stringify(this._handler.maintask_);
        this._handler.timetask = JSON.stringify(this._handler.timetask_);
        this._handler.items = JSON.stringify(this._handler.items_);
        this.commit();
    },

    // -------------- GET -------------- 

    //获得道具列表
    getItemList() {
        let items = this._handler.items_;
        let list = [];
        for(let id in items) {
            list.push({id: id, num: items[id]});
        }
        list.sort();
        return list;
    },

    getRecipeList() {
        let items = this._handler.items_;
        let list = [];
        for(let id in items) {
            if(id >= 3000 && id < 4000) {
                list.push(id);
            }
        }
        list.sort();
        return list;
    },

    //获得道具数据
    getItemCountById(id) {
        let num = this._handler.items_[id];
        return num || 0;
    },

    //获得道具数量
    getItemCount() {
        return Object.keys(this._handler.items_).length;
    },

    //背包空间
    getBagSpace() {
        return this._handler.bagspace;
    },

    //金币
    getCoin() {
        return this._handler.coin || 0;
    },

    //钻石
    getDiamond() {
        return this._handler.diamond || 0;
    },

    //头像
    getHeadUrl() {
        return this._handler.headurl || '';
    },

    //主任务
    getMainTask() {
        return this._handler.maintask_;
    },

    //计时任务
    getTimeTask() {
        return this._handler.timetask_;
    },

    //获得经验
    getExp() {
        return this._handler.exp || 0;
    },

    //获得体力
    getEnergy() {
        return this._handler.energy || 100;
    },

    // -------------- Mod -------------- 

    //售出道具
    sellItem(itemId, num, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        let current = this._handler.items_[itemId];
        let _current = current;
        if(current > 0) {
            current -= num;
            current = Math.max(0, current);
            let useNum = _current - current; //实际上售出数量
            let price = Wu57.ConfigMgr.ItemConfig[itemId].price;
            this._handler.coin += price * useNum;
            if(current <= 0) {
                delete this._handler.items_[itemId];
            } else {
                this._handler.items_[itemId] = current;
            }
            this._handler.items = JSON.stringify(this._handler.items_);
            this.commit();
            return;
        }
        this.onFailed();
    },

    //添加道具
    addItems(addItems, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        let isNotEnough = false;
        let count = addItems.length;
        for(let i=0; i<count; i++) {
            let itemInfo = addItems[i];
            let {id, num} = itemInfo;
            let has = this._handler.items_[id] || 0;
            has += num;
            if(this._handler.items_[id] == undefined) {
                if(Object.keys(this._handler.items_).length <= this.getBagSpace()) {
                    this._handler.items_[id] = Math.min(99, has);
                } else {
                    isNotEnough = true;
                }
            } else {
                this._handler.items_[id] = Math.min(99, has);
            }
        }

        if(isNotEnough) {
            Wu57.NoticeMgr.sendTip('仓库空间不足');
        }

        this._handler.items = JSON.stringify(this._handler.items_);
        this.commit();
    },
    
    //修改背包空间
    modBagSpace(val, cost, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        this._handler.diamond -= cost;
        this._handler.bagspace += val;
        this.commit();
    },

    //修改体力
    modEnergy(val, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        this._handler.energy += val;
        this._handler.energy = Math.max(0, Math.min(this._handler.energy, 100));

        this.commit();
    },

    //修改经验值
    modExp(val, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        this._handler.exp += val;
        this.commit();
    },

    //修改金币
    modCoin(val, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        this._handler.coin += val;
        this._handler.coin = Math.max(0, this._handler.coin);
        this.commit();  
    },

    //修改钻石
    modDiamond(val, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        this._handler.diamond += val;
        this._handler.diamond = Math.max(0, this._handler.diamond);
        this.commit();
    },

    //修改头像
    modHeadUrl(val, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        this._handler.headurl = val;
        this.commit();
    },

    //设置任务列表
    setMainTask(tasks, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        this._handler.maintask_ = tasks;
        this._handler.maintask = JSON.stringify(this._handler.maintask_);
        this.commit();
    },

    //添加一项主任务
    addMainTask(task, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        this._handler.maintask_[task.taskid] = task;
        this._handler.maintask = JSON.stringify(this._handler.maintask_);
        this.commit();
    },

    //删除一项主任务
    delMainTask(taskid, data, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        for(let i=0; i<data.removeItems.length; i++) {
            let itemInfo = data.removeItems[i];
            let [id, num] = itemInfo;
            let has = this._handler.items_[id];
            has -= num;
            if(has > 0) {
                this._handler.items_[id] = has;
            } else {
                delete this._handler.items_[id];
            }
        }
        this._handler.coin += data.coin;

        let list = this._handler.maintask_.list;
        let index = -1;
        for(let i=0; i<list.length; i++) {
            if(list[i] == taskid) {
                index = i;
                break;
            }
        }
        if(index >= 0) {
            this._handler.maintask_.list.splice(index);
            if(data.main) {
                if(this._handler.maintask_.main < 19999) {
                    this._handler.maintask_.main += 1;
                } else {
                    this._handler.maintask_.main = -1;
                }
                let main = this._handler.maintask_.main;
                if(main > 0) {
                    this._handler.maintask_.list.unshift(main);
                }
            }
        }
        
        this._handler.maintask = JSON.stringify(this._handler.maintask_);
        this._handler.items = JSON.stringify(this._handler.items_);
        this.commit();
    },

    //修改一项主任务
    modMainTask(task, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        let taskid = taskid;
        this._handler.maintask_[taskid] = {};
        for(key in task) {
            this._handler.maintask_[taskid][key] = task[key];
        }
        this._handler.maintask = JSON.stringify(this._handler.maintask_);
        this.commit();
    },

    //添加一项计时任务
    addTimeTask(task, removeItems, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        for(let i=0; i<removeItems.length; i++) {
            let itemInfo = removeItems[i];
            let [id, num] = itemInfo;
            let has = this._handler.items_[id];
            has -= num;
            if(has <= 0) {
                delete this._handler.items_[id];
            } else {
                this._handler.items_[id] = Math.max(0, has);
            }
        }
        this._handler.timetask_[task.taskid] = task;
        this._handler.timetask = JSON.stringify(this._handler.timetask_);
        this._handler.items = JSON.stringify(this._handler.items_);
        this.commit();
    },

    //删除一项计时任务
    delTimeTask(taskid, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        let task = this._handler.timetask_[taskid];
        if(this._handler.items_[task.food] == undefined) {
            let space = Object.keys(this._handler.items_).length;
            if(space >= this.getBagSpace()) {
                NoticeMgr.sendTip('仓库空间不足');
                return;
            }
        }
        let has = this._handler.items_[task.food] || 0;
        this._handler.items_[task.food] = Math.min(99, has + 1);

        delete this._handler.timetask_[taskid];
        this._handler.timetask = JSON.stringify(this._handler.timetask_);
        this._handler.items = JSON.stringify(this._handler.items_);
        this.commit();
    },

    //修改一项计时任务
    modTimeTask(task, onSuccess, onFailed) {
        this.onSuccess = onSuccess;
        this.onFailed = onFailed;

        let taskid = taskid;
        this._handler.timetask_[taskid] = {};
        for(key in task) {
            this._handler.timetask_[taskid][key] = task[key];
        }
        this._handler.timetask = JSON.stringify(this._handler.timetask_);
        this.commit();
    }
    
});
