// 漂浮

cc.Class({
    extends: cc.Component,

    properties: {
        Phase: {
            tooltip: '每秒移动的幅度',
            default: 64,
            type: cc.Integer
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onEnable () {
        let a1 = cc.moveBy(1, cc.v2(0, this.Phase));
        let a2 = a1.reverse();
        let ac = cc.sequence(a1, a2);
        this.node.runAction(cc.repeatForever(ac).easing(cc.easeBounceIn()));
    },

    onDisable() {
        this.node.stopAllActions();
    }
});
