// 事件调度管理器

cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this._eventMap = {};
    },

    //事件注册
    register(name, callfunc) {
        this._eventMap[name] = callfunc;
    },

    //事件注销
    unregister(name) {
        if(this._eventMap[name]) {
            delete this._eventMap[name];
        }
    },

    //事件触发
    emit(name, params) {
        let callfunc = this._eventMap[name];
        if(callfunc) {
            callfunc(params);
        } else {
            cc.warn(`${name}事件未注册`);
        }
    },

    //触发后注销
    once(name, params) {
        this.emit(name, params);
        this.unregister(name);
    },

    //注销所有事件
    clear() {
        this._eventMap = {};
    }
});
