// 任务Item

cc.Class({
    extends: cc.Component,

    properties: {
        TitleLab: cc.Label,
        TaskItems: [cc.Node]
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.node.on(cc.Node.EventType.TOUCH_END, this.checkTask, this);
        this.reset();
    },

    onDestroy() {
        this.stopLoop();
    },

    reset() {
        this.setTaskCount(0);
        this.TitleLab.string = '';
        this.taskInfo = 0;
    },

    checkTask() {
        if(this.taskInfo) {
            let params = Object.assign({}, this.taskInfo);
            Wu57.EventMgr.emit(Wu57.Constant.EventCode.ClickTaskItem, params);
        }
    },

    setTaskCount(count) {
        for(let i=1; i<=this.TaskItems.length; i++) {
            let taskItem = this.TaskItems[i-1];
            taskItem.active = i <= count;
        }
    },

    setTaskId(taskId) {
        let taskInfo = Wu57.ConfigMgr.getTaskInfo(taskId);
        if(taskInfo) {
            this.taskInfo = Object.assign({}, taskInfo);
            this.runLoop();
        }
    },

    runLoop() {
        this.stopLoop();
        this.refresh();
        this.schedule(this.refresh, 2);
    },

    stopLoop() {
        this.unschedule(this.refresh);
    },

    refresh() {
        if(!this.taskInfo) return;

        this.TitleLab.string = (this.taskInfo.type == 1 ? '主线' : '支线') + this.taskInfo.id;
        let count = this.taskInfo.list.length;
        for(let i=1; i<=this.TaskItems.length; i++) {
            let taskItem = this.TaskItems[i-1];
            let isEnable = i <= count;
            taskItem.active = isEnable;
        }
        for(let i=0; i<count; i++) {
            let listData = this.taskInfo.list[i];
            let taskItem = this.TaskItems[i];
            let numLab = taskItem.getChildByName('NumLab').getComponent(cc.Label);
            let itemSprite = taskItem.getChildByName('ItemSprite').getComponent(cc.Sprite);
            let itemId = listData[0];
            let itemNum = Wu57.DatabaseMgr.getItemCountById(itemId);
            numLab.string = `${itemNum}/${listData[1]}`;
            itemSprite.spriteFrame = Wu57.ResMgr.getItemImage(itemId);
        }
    },
    
});
