cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.Distance = Game.MathUtil.randomInt(10, this.node.width);
    },

    start () {
        this.node.runAction(
            cc.repeatForever(
                cc.moveBy(1, cc.v2(-this.Distance, 0))
            )
        );
    },

    // update (dt) {},
});
