// 菜品制作页-菜品单项

cc.Class({
    extends: cc.Component,

    properties: {
        FoodImg: cc.Sprite,
        NameLab: cc.Label,
        TimeLab: cc.Label,
        HasLab: cc.Label,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.MatListNode = this.node.getChildByName('MatList');
        this.isMaterialEnough = true;
        this.CookBtnLab = this.node.getChildByName('CookBtn').getChildByName('Background').getChildByName('Label').getComponent(cc.Label);
    },

    onDestroy() {
        
    },

    setFood(food, param) {
        this.param = param;
        this.foodCfg = food;
        this.itemCfg = Wu57.ConfigMgr.getItemInfo(food.id);
        this.FoodImg.spriteFrame = Wu57.ResMgr.getItemImage(food.id);
        this.NameLab.string = this.itemCfg.name;
        this.TimeLab.string = Wu57.UtilsMgr.TimeUtil.secondToHour(food.time);
        this.HasLab.string = Wu57.DatabaseMgr.getItemCountById(food.id);

        let materials = food.material;
        let count = materials.length;
        let children = this.MatListNode.children;
        for(let i=0; i<4; i++) {
            let m = materials[i];
            let enable = i < count;
            let img = children[i];
            img.active = enable;
            if(enable) {
                let numLab = img.getChildByName('NumLab');
                let imgCom = img.getComponent(cc.Sprite);
                let numCom = numLab.getComponent(cc.Label);
                let has = Wu57.DatabaseMgr.getItemCountById(m[0]);
                let need = m[1];
                let enough = has >= need;
                imgCom.spriteFrame = Wu57.ResMgr.getItemImage(m[0]);
                numLab.color = enough ? cc.Color.WHITE : cc.Color.RED;
                numCom.string = need;
                if(!enough) {
                    this.isMaterialEnough = false;
                }
            }
        }

        this.CookBtnLab.string = this.isMaterialEnough ? '制 作' : '获 取';
    },

    cookBtnClick() {
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.Click);

        if(this.isMaterialEnough) {
            this.cook();
        } else {
            this.redirect();
        }
    },

    cook() {
        let food = this.foodCfg.id;
        let time = this.foodCfg.time;

        let seat = this.param.seat;
        let target = this.param.target;
        let removeItems = this.foodCfg.material;
        let ui = this.param.ui;
        let task = {
            taskid: 'task' + seat,
            limit: time,
            time: time,
            seat: seat,
            food: food
        };
        Wu57.DatabaseMgr.addTimeTask(task, removeItems, function onSuccess(res) {
            target.setTask(task);
            ui.onExit();
        }, function onFailed(res) {
            Wu57.NoticeMgr.checkNetwork();
        });
    },

    redirect() {
        cc.find('Canvas/MainUI/FoodListUI').getComponent('FoodListUI').onExit();
        cc.find('Canvas/MainUI/GetwayUI').getComponent('GetwayUI').onEnter(this.foodCfg.material);
    }
});
