
cc.Class({
    extends: cc.Component,

    properties: {
        MaxRetryChances: {  //重试机会
            default: 1,
            type: cc.Integer,
        },
        CurRetryChances: {
            default: 1,
            type: cc.Integer,
            readonly: true,
        }, 
        MaxTime: 30,        //限定时间
        CurTime: {          //当前时间
            default: 0,
            type: cc.Integer,
            readonly: true,
        },
        MaxExpNum: {        //最大经验值
            default: 100,
            type: cc.Integer,
        },
        CurExpNum: {        //当前经验值
            default: 0,
            type: cc.Integer,
            readonly: true,
        },
        AddExpNum: {
            tooltip: '每一轮正确后增加的研究点',
            type: cc.Integer,
            default: 5
        },
        PlaySteps: {
            tooltip: '每一轮的步数',
            type: cc.Integer,
            default: 3
        },
        BrickNormalFrame: {
            type: cc.SpriteFrame,
            tooltip: '砖块正常时的精灵帧',
            default: null
        },
        BrickClickFrame: {
            type: cc.SpriteFrame,
            tooltip: '砖块按下时的精灵帧',
            default: null
        },

        /////////////////////////////
        StartUI: cc.Node,

        /////////////////////////////
        PlayUI: cc.Node,
        PlayBricks: [cc.Node],
        PlayProbar: cc.ProgressBar,
        PlayTimeLab: cc.Label,
        PlayExpLab: cc.Label,

        /////////////////////////////
        OverUI: cc.Node,
        OverLab: cc.Label,

        /////////////////////////////
        WinUI: cc.Node,
    },

    // LIFE-CYCLE CALLBACKS:

    //UI进入
    onEnter() {
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.MainBg1);

        this.node.active = true;
        this.node.setPosition(0, cc.winSize.height);
        let act = cc.moveTo(Wu57.Constant.UIEnterExitTime, cc.v2(0, 0)).easing(cc.easeBackInOut());
        this.node.runAction(act);

        this.CurRetryChances = this.MaxRetryChances;
        this.openStartUI();
    },

    //UI退出
    onExit() {
        this.stopLoop();

        let a2 = cc.moveTo(Wu57.Constant.UIEnterExitTime, cc.v2(0, cc.winSize.height));
        let a3 = cc.callFunc(() => {
            cc.find('Canvas').getComponent('MainScene').onEnter();
            this.node.active = false;
        });
        let act = cc.sequence(a2, a3).easing(cc.easeBackInOut());
        this.node.runAction(act);
    },

    //打开开始界面
    openStartUI() {
        this.PlayUI.active = false;
        this.OverUI.active = false;
        this.WinUI.active = false;
        this.StartUI.active = true;
    },

    //打开游戏界面
    openPlayUI() {
        this.StartUI.active = false;
        this.OverUI.active = false;
        this.WinUI.active = false;
        this.PlayUI.active = true;

        this.isGuide = true;
        this.WrongNum = 0;
        this.CurExpNum = 0;
        this.CurTime = this.MaxTime;

        this.initBricks();
        this.refreshPlayUI();
        this.nextRound();
        this.runLoop();
    },

    //打开结束界面
    openOverUI() {
        this.StartUI.active = false;
        this.PlayUI.active = false;
        this.WinUI.active = false;
        this.OverUI.active = true;
    },

    //打开成功界面
    openWinUI() {
        this.StartUI.active = false;
        this.PlayUI.active = false;
        this.OverUI.active = false;
        this.WinUI.active = true;
    },

    //初始化砖块
    initBricks() {
        for(let i=0; i<this.PlayBricks.length; i++) {
            let brick = this.PlayBricks[i];
            brick.index = i;
            brick.on(cc.Node.EventType.TOUCH_START, this.onClickBrick, this);
            brick.getComponent(cc.Sprite).spriteFrame = this.BrickNormalFrame;
        }
    },

    //玩家点击砖块
    onClickBrick(event) {
        if(this.isGuide) return;
        
        let brick = event.target;
        this.clickBrick(brick);
        
        let mine = brick.index + 1;
        let current = this.brickOrder[this.clickIndex];
        let check = current == mine;
        this.clickIndex += 1;

        if(!check) {
            this.WrongNum++;
        }

        if(this.clickIndex >= this.brickOrder.length) {
            if(this.WrongNum == 0) {
                Wu57.NoticeMgr.sendTip(`经验+${this.AddExpNum}`);
                this.CurExpNum += this.AddExpNum;
            } else {
                Wu57.NoticeMgr.sendTip(`您错了${this.WrongNum}个`);
            }
            this.clickIndex = 0;
            this.WrongNum = 0;
            this.isGuide = true;
            this.scheduleOnce(this.nextRound, 1);
        }
    },

    //模拟点击砖块
    clickBrick(brick) {
        let sprite = brick.getComponent(cc.Sprite);
        sprite.spriteFrame = this.BrickClickFrame;
        brick.runAction(cc.sequence(
            cc.delayTime(0.5),
            cc.callFunc(() => {
                sprite.spriteFrame = this.BrickNormalFrame;
            })
        ));
    },

    //随机步骤
    randomSteps() {
        let steps = this.PlaySteps;
        let random = Wu57.UtilsMgr.MathUtil.randomInt(1, 100);
        //random: (3,0.75)-(4,0.2)-(5,0.05)
        if(random <= 75) {
            steps = 3;
        } else if(random <= 95) {
            steps = 4;
        } else {
            steps = 5;
        }
        return Wu57.UtilsMgr.ArrayUtil.randomFrom9(steps);
    },

    //进入下一轮
    nextRound() {
        this.guide = true;
        this.clickIndex = 0;
        this.brickOrder = this.randomSteps();

        let seq = [];
        for(let i=0; i<this.brickOrder.length; i++) {
            let a1 = cc.callFunc(() => {
                let order = this.brickOrder[i] - 1;
                let brick = this.PlayBricks[order];
                this.clickBrick(brick);
            });
            let a2 = cc.delayTime(0.5);
            seq.push(a1, a2);
        }
        seq.push(cc.callFunc(() => {
            this.isGuide = false;
        }));
        this.node.runAction(cc.sequence(seq));
    },

    //启动循环
    runLoop() {
        this.stopLoop();
        this.schedule(this.refresh, 0.1);
        this.schedule(this.refreshPlayUI, 0.05);
    },

    //停止循环
    stopLoop() {
        this.unschedule(this.refresh);
        this.unschedule(this.refreshPlayUI);
    },

    //刷新数据
    refresh() {
        if(this.isGuide) return;

        this.CurTime -= 0.1;
        if(this.CurTime < 0) {
            this.stopLoop();
            this.openOverUI();
            return;
        }
    },

    //刷新界面
    refreshPlayUI() {
        this.PlayProbar.progress = this.CurExpNum / this.MaxExpNum;
        this.PlayExpLab.string = `${this.CurExpNum}/${this.MaxExpNum}`;
        this.PlayTimeLab.string = this.CurTime.toFixed(1) + 's';
        this.OverLab.string = this.CurRetryChances == 0 ? '退  出' : '重  试';
        if(this.CurExpNum >= this.MaxExpNum) {
            this.stopLoop();
            this.openWinUI();
            return;
        }
    },

    //结束
    onOver() {
        if(this.CurRetryChances == 0) {
            this.onExit();
        } else {
            this.CurRetryChances -= 1;
            this.openPlayUI();
        }
    },

    //准备开始游戏
    readyGame() {
        let self = this;
        Wu57.NoticeMgr.open('提示', '开始游戏需要扣除3点体力值，是否继续？', () => {
            let has = Wu57.DatabaseMgr.getEnergy();
            if(has < 3) {
                Wu57.NoticeMgr.sendError('体力不足');
            } else {
                Wu57.DatabaseMgr.modEnergy(-3, () => {
                    self.openPlayUI();
                });
            }
        })
    },

    //界面按钮事件
    onButtonClicked(event, data) {
        event.stopPropagation();
        switch(data) {
            case 'Start':
                this.readyGame();
                break;
            case 'Over':
                this.onOver();
                break;
            case 'Win':
                this.onExit();
                break;
        }
    }
});
