
cc.Class({
    extends: cc.Component,

    properties: {
        Image: cc.Sprite,
        NumLab: cc.Label,
        Right: cc.Node,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.unselected();
    },

    reset() {
        this.itemInfo = null;
        this.node.off(cc.Node.EventType.TOUCH_END, this.onselected, this);
        this.node.on(cc.Node.EventType.TOUCH_END, this.onselected, this);
        this.unselected();
    },

    setItem(itemInfo) {
        this.reset();
        this.itemInfo = itemInfo;
        this.Image.spriteFrame = Wu57.ResMgr.getItemImage(itemInfo.id);
        this.NumLab.string = itemInfo.num;
    },

    onselected(event) {
        event.stopPropagation();

        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.Click);
        this.Right.active = !this.Right.active;
        let selected = this.Right.active;
        Wu57.EventMgr.emit(Wu57.Constant.EventCode.ClickBagItem, {
            sender: this,
            selected: selected
        });
    },

    unselected() {
        this.Right.active = false;
    }
});
