// 上

cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.node.y = cc.winSize.height + this.node.height;
    },

    onEnter () {
        let offset = cc.v2(0, -this.node.height);
        this.node.runAction(cc.moveBy(Wu57.Constant.UIEnterExitTime, offset).easing(cc.easeBackInOut()));
    },

    onExit () {
        let offset = cc.v2(0, this.node.height);
        this.node.runAction(cc.moveBy(Wu57.Constant.UIEnterExitTime, offset).easing(cc.easeBackInOut()));
    }

});
