// 计时器管理

// 计时任务
class ScheduleTask {
    constructor(taskInfo) {
        for(let key in taskInfo) {
            this[key] = taskInfo[key];
        }
        this.done = false;
    }

    countdown(t) {
        if(this.done) {
            return;
        }

        let time = this.time - t;
        this.time = Math.max(0, time);
        if(this.time <= 0) {
            this.done = true;
        }
    }

    finish() {
        this.time = 0;
        this.done = true;
    }
};

cc.Class({
    extends: cc.Component,

    properties: {
        speed: {
            default: 1,
            type: cc.Integer,
            readonly: true
        },
        running: {
            default: false,
            readonly: true,
        },
    },

    onLoad() {
        this._adjust_time = 0;
        this.taskList = [];
    },

    onDestroy() {
        this.unschedule(this.refreshTaskList);
    },

    //刷新任务列表
    refreshTaskList() {
        if(!this.running) return;
        let count = this.taskList.length;
        if(count == 0) return;

        let time = this.speed;
        if(this._adjust_time > 0) {
            time = this._adjust_time;
            this._adjust_time = 0;
        }

        for(let i=0; i<count; i++) {
            let task = this.taskList[i];
            task.countdown(time);
        }
    },

    //获得速度
    getSpeed() {
        return this.speed;
    },

    //设置速度
    setSpeed(speed, time) {
        let prespeed = this.speed;
        this.speed = speed;
        if(time && time > 0) {
            this.scheduleOnce( () => {
                this.speed = prespeed;
            }, time);
        }
    },

    //运行
    run() {
        if(this.running) {
            return;
        }
        this.running = true;
        this.schedule(this.refreshTaskList, 1);
    },

    //继续
    resume() {
        this.running = true;
    },

    //暂停
    pause() {
        this.running = false;
    },

    //新任务
    addTask(taskInfo) {
        let taskId = taskInfo.objectId;
        let task = this.getTask(taskId);
        if(task) {
            task.time = Math.min(taskInfo.time, task.time);
        } else {
            task = new ScheduleTask(taskInfo);
            this.taskList.push(task);
        }
        return task;
    },

    //删除任务
    delTask(taskId) {
        let index = -1;
        for(let i=0; i<this.taskList.length; i++) {
            let task = this.taskList[i];
            if(taskId == task.taskId) {
                index = i;
                break;
            }
        }

        if(index >= 0) {
            this.taskList.splice(index, 1)[0].del();
        }
    },

    //获得任务
    getTask(taskId) {
        let ret = null;
        for(let i=0; i<this.taskList.length; i++) {
            let task = this.taskList[i];
            if(taskId == task.taskId) {
                ret = task;
                break;
            }
        }
        return ret;
    },

    //调整时间
    adjustTime(time) {
        if(this.taskList.length > 0) {
            this._adjust_time = time;
        }
    }
});
