const RequestType = {
    Get: 'GET',
    Post: 'POST'
};

const URL_PREFIX = 'https://wu57.cn/magic/';

cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.RequestType = RequestType;
    },

    init () {
        
    },

    _sendHttpRequest(url, type=RequestType.Get) {
        let xhr = new XMLHttpRequest();
        xhr.onreadystatechange = function () {
            if (xhr.readyState == 4 && (xhr.status >= 200 && xhr.status < 400)) {
                let response = JSON.parse(xhr.responseText);
                console.log(response);
            }
        }
        xhr.onerror = function () {
            console.log(`网络错误，请重试。`, xhr.status);
        }
        xhr.ontimeout = function () {
            console.log(`网络超时`);
        }
        xhr.timeout = 100000; //设置超时
        xhr.open(type, url, true);
        xhr.send();

        console.log(`type: ${type}, url: ${url}`);
    },

    requestGet(where) {
        let url = URL_PREFIX+where;
        this._sendHttpRequest(url);
    },

    requestPost(where, params) {
        let url = URL_PREFIX+where+'?';
        let len = Object.keys(params).length;
        for(let key in params) {
            len--;
            url += `${key}=${params[key]}`
            if(len > 0) {
                url += '&'
            }
        }

        this._sendHttpRequest(url, 'POST');
    },

    // update (dt) {},
});
