// 获取途径

cc.Class({
    extends: cc.Component,

    properties: {
        TitleLab: cc.Label,
        GetWayPage: cc.Node,
        MaskTouch: require('MaskTouch')
    },

    // LIFE-CYCLE CALLBACKS:

    onEnter (param) {
        this.node.active = true;
        this.node.scale = 0;
        this.MaskTouch.enableTouch();

        this.node.runAction(cc.scaleTo(0.5, 1).easing(cc.easeBackInOut()));
        this.reload(param);
    },

    onExit () {
        this.node.runAction(cc.scaleTo(0.5, 0).easing(cc.easeBackInOut()));
        this.scheduleOnce(() => {
            this.MaskTouch.disableTouch();
            this.node.active = false;
        }, 0.6);
    },

    reload (materials) {
        let children = this.GetWayPage.children;
        let count = materials.length;
        for(let i=0; i<4; i++) {
            let item = children[i];
            let isEnable = i < count;
            item.active = isEnable;
            if(isEnable) {
                let [id, num] = materials[i];
                let itemImg = item.getChildByName('ItemImg').getComponent(cc.Sprite);
                let numLab = item.getChildByName('NumLab').getComponent(cc.Label);
                let nameLab = item.getChildByName('NameLab').getComponent(cc.Label); 
                let tipLab = item.getChildByName('TipLab').getComponent(cc.Label); 
                let goBtn = item.getChildByName('GoBtn');
                let has = Wu57.DatabaseMgr.getItemCountById(id);
                let cfg = Wu57.ConfigMgr.getItemInfo(id);
                nameLab.string = cfg.name;
                tipLab.string = cfg.where_desc;
                numLab.string = `${has}/${num}`;
                numLab.node.color = has >= num ? cc.Color.WHITE : cc.Color.RED;
                itemImg.spriteFrame = Wu57.ResMgr.getItemImage(id);
                goBtn.active = has < num;
                goBtn.where = cfg.where;
            }
        }
    },

    redirect(sender) {
        this.onExit();
        let subgame = `SubGameUI${sender.where}`;
        cc.find('Canvas').getComponent('MainScene').openSubGame(subgame);
    },

    onClicked(event, code) {
        event.stopPropagation();
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.Click);

        switch(code) {
            case 'Close':
                this.onExit();
                break;
            case 'Go':
                this.redirect(event.target);
                break;
        }
    }
});
