const AudioName = {
    Click: 'Click',
    Complete: 'Complete',
    Dodge: 'Dodge',
    Enter: 'Enter',
    Heal: 'Heal',
    LevelUp: 'LevelUp',
    Revive: 'Revive',
    MainBg1: 'MainBg1',
    MainBg2: 'MainBg2',
    PlayBg1: 'PlayBg1',
    PlayBg2: 'PlayBg2',
    Hammer: 'Hammer',
    Win: 'Win',
    Fail: 'Fail',
};

cc.Class({
    extends: cc.Component,

    properties: {
        AudioContainer: cc.Node,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this._music = null;
        this._mute = false;
        this._volume = 1;
        this.AudioName = AudioName;
        this.initAudios();
    },

    //初始化音频源
    initAudios() {
        this._audioList = {};
        this._audioComs = this.AudioContainer.getComponents(cc.AudioSource);
        for(let i=0; i<this._audioComs.length; i++) {
            let source = this._audioComs[i];
            this._audioList[source.clip.name] = source;
        }
    },

    //应用配置到音频源
    apply(key, val) {
        for(let i=0; i<this._audioComs.length; i++) {
            let source = this._audioComs[i];
            source[key] = val;
        }
    },

    //获取音频源
    getAudioSource(name) {
        return this._audioList[name];
    },

    //设置音量
    setVolumn(vol) {
        if(vol != this._volume) {
            this._volume = vol;
            this.apply('volume', vol);
        }
    },

    //获取音量
    getVolumn() {
        return this._volume;
    },

    //开启静音
    enableMute() {
        if(!this._mute) {
            this._mute = true;
            this.apply('mute', true);
            Wu57.StorageMgr.setMusicOpen(false);
        }
    },

    //关闭静音
    disableMute() {
        if(this._mute) {
            this._mute = false;
            this.apply('mute', false);
            Wu57.StorageMgr.setMusicOpen(true);
        }
    },

    //播放
    play (name) {
        if(this._last) {
            this._last.stop();
        }

        let source = this.getAudioSource(name);
        if(source) {
            if(source.loop && this._music) {
                //结束其他循环的音乐
                this._music.stop();
            }
            if(source.loop) {
                this._music = source;
            } else {
                this._last = source;
            }
            source.play();
        }
    },

    //停止
    stop (name) {
        let source = this.getAudioSource(name);
        if(source) {
            source.stop();
        }
        if(!source.loop) {
            this._last = null;
        }
    },

    //暂停
    pause (name) {
        let source = this.getAudioSource(name);
        if(source) {
            source.pause();
        }
    },

    //恢复
    resume (name) {
        let source = this.getAudioSource(name);
        if(source) {
            source.resume();
        }
    },

    //重播
    rewind (name) {
        let source = this.getAudioSource(name);
        if(source) {
            source.rewind();
        }
    },

    //退到后台时暂停
    pauseMusic() {
        if(this._music) {
            this._music.stop();
            console.log('[暂停音乐]');
        }
        if(this._last) {
            this._last.stop();
            this._last = null;
        }
    },

    //返回前台时恢复
    resumeMusic() {
        console.log('[恢复音乐]');
        if(this._music) {
            this._music.play();
        }
    }
});
