// 面包块

cc.Class({
    extends: cc.Component,

    properties: {
        Speed: 50,
        SpriteFrames: [cc.SpriteFrame],
        Right: cc.Node,
        Wrong: cc.Node,
        ColliderArea: cc.Node,
    },

    init() {
        this.unmask();
        
        let node_width = this.node.width * 0.5;
        let limit_lower = node_width, limit_upper = this.node.parent.width - node_width;
        this.node.x = Wu57.UtilsMgr.MathUtil.randomInt(limit_lower, limit_upper);
        this.node.y = this.node.parent.height + 10;

        let size = this.SpriteFrames.length-1;
        let index = Wu57.UtilsMgr.MathUtil.randomInt(0, size);
        this.setPiece(index);
        this.enableCollider();

        this.isrunning = true;
    },

    enableCollider() {
        this.ColliderArea.active = true;
    },

    disableCollider() {
        this.ColliderArea.active = false;
    },

    unmask() {
        this.Right.active = false;
        this.Wrong.active = false;
    },

    maskRight() {
        this.Right.active = true;
        this.Wrong.active = false;
    },

    maskWrong() {
        this.Right.active = false;
        this.Wrong.active = true;
    },

    setPiece(index) {
        let frame = this.SpriteFrames[index];
        this.node.index = index+1;
        this.node.getComponent(cc.Sprite).spriteFrame = frame;
    },

    recycle() {
        this.isrunning = false;
        Wu57.PoolMgr.returnBreadPiece(0, this.node);
    },

    lateUpdate (dt) {
        if(!this.isrunning) {
            return;
        }

        this.node.y -= this.Speed * dt;
        if(this.node.y <= -this.node.height) {
            this.recycle();
            return;
        }
    },
});
