// 加解密接口封装
const Cipher = (() => {
    const Encrypt = require('encryptjs');
    return {
        encode: (text) => {
            return Encrypt.encrypt(text, Wu57.Constant.ProjectName, Wu57.Constant.EncryptBits);
        },
        decode: (text) => {
            return Encrypt.decrypt(text, Wu57.Constant.ProjectName, Wu57.Constant.EncryptBits);
        }
    }
})();

module.exports = Cipher;