/////////// Platform ///////////
// MOBILE_BROWSER : 100
// DESKTOP_BROWSER : 101
// EDITOR_PAGE : 102
// EDITOR_CORE : 103
// WECHAT_GAME : 104
// QQ_PLAY : 105
// FB_PLAYABLE_ADS : 106
// BAIDU_GAME : 107
// VIVO_GAME : 108
// OPPO_GAME : 109
///////////////////////////////

cc.Class({
    extends: cc.Component,

    properties: {
        UpdateContainer: cc.Node,
        LoginContainer: require('LoginContainer'),
        TipLab: cc.Label,
        VersionLab: cc.Label,
        ProgressBar: cc.ProgressBar,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.MaskTouch = this.UpdateContainer.getComponent('MaskTouch');
        this.MaskTouch.enableTouch();
    },

    //检查资源包
    checkUpdate() {
        console.log('当前平台: ', cc.sys.platform);
        this.TipLab.node.active = true;
        this.TipLab.node.opacity = 255;
        if (cc.sys.platform == cc.sys.WECHAT_GAME) {
            this.checkVersion();
        } else {
            this.onComplete();
        }
    },

    //检查版本号
    checkVersion() {
        let versionLocal = Wu57.StorageMgr.getVersionCode();
        versionLocal = parseInt(versionLocal);
        this.VersionLab.node.active = true;
        this.VersionLab.string = `v.${versionLocal}`;
        this.versionLocal = versionLocal;
        this.TipLab.string = '正在检测资源包...';
        console.log('检查版本号:', versionLocal);

        let self = this;
        let url = Wu57.Constant.SERVER_URL_PRE + Wu57.Constant.SERVER_VERSION;
        let xhr = new XMLHttpRequest();
        xhr.onreadystatechange = function () {
            if (xhr.readyState == 4 && (xhr.status >= 200 && xhr.status < 400)) {
                let response = xhr.responseText;
                if (response && parseInt(response)) {
                    if (parseInt(response) > self.versionLocal) {
                        self.versionServer = parseInt(response);
                        self.downloadRes();
                    } else {
                        self.TipLab.string = '资源包检测完成';
                        self.onComplete();
                    }
                } else {
                    let err = `网络错误: ${xhr.statusText}，请重试。`;
                    Wu57.NoticeMgr.open('有点问题哦~', err, () => {
                        self.checkVersion();
                    });
                }
            }
        };
        xhr.onerror = function () {
            let err = `网络错误: ${xhr.statusText}，请重试。`;
            Wu57.NoticeMgr.open('有点问题哦~', err, () => {
                self.checkVersion();
            });
        };
        xhr.ontimeout = function () {
            let err = '网络请求超时，请重试。';
            Wu57.NoticeMgr.open('有点问题哦~', err, () => {
                self.checkVersion();
            });
        };
        xhr.timeout = 300000; //设置超时
        xhr.open("GET", url, true);
        xhr.send();
    },

    //保存版本号
    saveVersion() {
        this.versionLocal = this.versionServer;
        this.VersionLab.string = '版本 ' + this.versionServer;
        Wu57.StorageMgr.setVersionCode(this.versionServer);
    },

    //下载资源包
    downloadRes() {
        this.ProgressBar.node.active = true;
        this.ProgressBar.progress = 0;
        this.TipLab.string = '正在接收资源包... (0%)';

        let self = this;
        let url = Wu57.Constant.SERVER_URL_PRE + Wu57.Constant.SERVER_RES_ZIP;
        let downloadTask = wx.downloadFile({
            url: url,
            success(res) {
                self._tempFilePath = res.tempFilePath;
                self.unzipRes();
            },
            fail(res) {
                let err = '资源包接收失败，请重试。';
                Wu57.NoticeMgr.open('有点问题哦~', err, () => {
                    self.downloadRes();
                });
            }
        });
        downloadTask.onProgressUpdate((res) => {
            let pro = res.progress; // 下载的进度
            self.ProgressBar.progress = pro / 100;
            self.TipLab.string = `正在接收资源包... (${pro}%)`;
        });
    },

    //解压资源包
    unzipRes() {
        let self = this;
        self.TipLab.string = '正在解压资源包...';
        let fsm = wx.getFileSystemManager();
        fsm.unzip({
            zipFilePath: self._tempFilePath,
            targetPath: wx.env.USER_DATA_PATH,
            success(res) {
                self.TipLab.string = '资源包解压成功，祝您游戏愉快';
                self.ProgressBar.node.active = false;
                self.TipLab.node.runAction(
                    cc.sequence(
                        cc.fadeOut(0.5),
                        cc.callFunc(() => {
                            self.onComplete();
                            self.saveVersion();
                        })
                    )
                );
                console.log(wx.env, self._tempFilePath);
                fsm.unlink({
                    filePath: self._tempFilePath,
                    success() {
                        console.log('删除下载资源成功')
                    },
                    fail(res) {
                        console.log('删除下载资源失败', res.errMsg);
                    }
                });
            },
            fail(res) {
                let err = '资源包解压失败，请重试。';
                Wu57.NoticeMgr.open('有点问题哦~', err, () => {
                    self.unzipRes();
                });
            }
        })
    },

    //Loading完成
    onComplete() {
        if (cc.sys.platform == cc.sys.WECHAT_GAME) {
            //是否已存在用户信息
            let key = Wu57.Constant.getStorageKey(Wu57.Constant.StorageKey.WechatUserInfo);
            let isLogin = cc.sys.localStorage.getItem(key);
            if (isLogin) {
                this.onStart();
                return;
            }

            //获取用户信息
            let self = this;
            wx.getSetting({
                success(res) {
                    if (res.authSetting["scope.userInfo"]) {
                        console.log('[微信用户已登录]', res);
                        self.createAuthorityBtn();
                        //`self.onStart();
                    } else {
                        console.log('[微信用户未登录]', res);
                        self.loginWechat();
                    }
                }
            });

        } else {
            this.onStart();
        }
    },

    //设置用户信息
    setUserInfo() {
        if (!this._userInfo) {
            console.log('未获取到微信用户信息');
            return;
        }
        let res = this._userInfo;
        console.log('[微信用户授权信息]', res);
        let userInfo = {};
        Object.assign(userInfo, res.userInfo);
        userInfo.sign = res.singature;
        let key = Wu57.Constant.getStorageKey(Wu57.Constant.StorageKey.WechatUserInfo);
        userInfo = JSON.stringify(userInfo);
        cc.sys.localStorage.setItem(key, userInfo);
        console.log(`[你好,${res.userInfo.nickName}]`);
    },

    //微信登录
    loginWechat() {
        let self = this;
        wx.login({
            success(res) {
                console.log('[微信登录成功]', res);
                self.createAuthorityBtn();
            },
            fail(res) {
                console.log('[微信登录失败]', res);
            }
        });
    },

    //创建微信授权按钮
    createAuthorityBtn() {
        let btnSize = this.node.getContentSize();
        let frameSize = cc.view.getFrameSize();
        let winSize = cc.winSize;
        let left = 0;
        let top = 0;
        let width = btnSize.width / winSize.width * frameSize.width;
        let height = btnSize.height / winSize.height * frameSize.height;

        console.log("创建授权按钮：", left, top, width, height);
        let wxBtn = wx.createUserInfoButton({
            type: 'text',
            text: '', //微信授权
            style: {
                left: left,
                top: top,
                width: width,
                height: height,
                lineHeight: height,
                backgroundColor: '',
                color: '#000000',
                textAlign: 'center',
                fontSize: 24,
                borderRadius: 4
            }
        });

        let self = this;
        wxBtn.onTap(res => {
            if (res.userInfo) {
                wxBtn.destroy();
                self._userInfo = res;
                self.setUserInfo();
                self.onStart();
            } else {
                cc.log('[微信用户拒绝授权]:', res);
            }
        });

        this.TipLab.node.active = true;
        this.TipLab.node.stopAllActions();
        this.TipLab.node.runAction(cc.repeatForever(
            cc.sequence(
                cc.fadeIn(0.5),
                cc.delayTime(1),
                cc.fadeOut(0.4),
                cc.delayTime(0.1),
            )
        ));
        //为提升您的体验\n我们需要您的授权\n
        this.TipLab.string = '点击任意位置继续';
    },

    //设置提示消息
    setTip(tip) {
        this.TipLab.node.active = true;
        this.TipLab.string = tip;
    },

    //开始游戏
    onStart() {
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.MainBg1);
        
        let LoginContainer = cc.find('Canvas/Background/LoginContainer');
        let LoginContainerCom = LoginContainer.getComponent('LoginContainer');
        LoginContainerCom.registerEvents();

        let session = Wu57.StorageMgr._getSession();
        if (session) {
            Wu57.LoginMgr.login(session);
        } else {
            LoginContainerCom.openButtons();
        }

        console.log('预备开始');
        this.TipLab.node.stopAllActions();
        this.TipLab.node.active = false;
    },
});