
cc.Class({
    extends: cc.Component,

    properties: {
        TitleLab: cc.Label,
        TipLab: cc.Label,
        NameLab: cc.Label,
        NumLab: cc.Label,
        ItemImage: cc.Sprite,
        Slider: cc.Slider,
        SliderPro: cc.ProgressBar,
        MaskTouch: require('MaskTouch'),
        MainUI: require('BagMainUI')
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        
    },

    onEnable() {
        this.MaskTouch.enableTouch();
        this.node.setScale(0);
        let a1 = cc.scaleTo(Wu57.Constant.UIEnterExitTime, 1);
        let act = a1.easing(cc.easeBackInOut());
        this.node.runAction(act);
    },

    setItem (itemId, num=0) {
        this.itemId = itemId;
        this.itemNum = num;
        this.itemInfo = Wu57.ConfigMgr.getItemInfo(itemId);
        this.ItemImage.spriteFrame = Wu57.ResMgr.getItemImage(itemId);
        
        this.setProgress(1);

        this.scheduleOnce( () => {
            this.Slider.progress = 1;
        }, 0.1);
    },

    setProgress(pro) {
        this.Slider.progress = pro;
        this.SliderPro.progress = pro;

        let num = Math.floor(this.itemNum * pro);
        this.NameLab.string = this.itemInfo.name + 'x' + num;
        this.NumLab.string = 'x' + this.itemInfo.price * num;
        this.curNum = num;
    },

    sliderMove(event) {
        this.setProgress(event.progress);
    },

    sell () {
        Wu57.DatabaseMgr.sellItem(this.itemId, this.curNum, () => {
            let cfg = Wu57.ConfigMgr.ItemConfig[this.itemId];
            let price = this.curNum * cfg.price;
            Wu57.NoticeMgr.sendTip(`获得金币x${price}`);
            this.MainUI.refreshPages();
            this.close();
        }, () => {
            Wu57.NoticeMgr.sendError('卖出失败');
        });
    },

    close () {
        let a1 = cc.scaleTo(Wu57.Constant.UIEnterExitTime, 0);
        let a2 = cc.callFunc(() => {
            this.node.active = false;
        });
        let act = cc.sequence(a1, a2).easing(cc.easeBackInOut());
        this.node.runAction(act);
    },

    onButtonClicked (event, code) {
        event.stopPropagation();

        switch(code) {
            case 'Cancel':
                this.close();
                break;
            case 'Sell':
                this.sell();
                break;
        }
    }

});
