const Direction = {
    Left: -1,
    None: 0,
    Right: 1,
};

cc.Class({
    extends: cc.Component,

    properties: {
        Speed: {
            tooltip: '限定角色运动时的最大速度',
            default: cc.v2(320, 640)
        },
        InitialSpeedX: {
            tooltip: 'X轴方向的初始速度',
            default: 192,
            type: cc.Integer
        },
        Accelerate: {
            tooltip: '加速度：此数值越大，达到最大速度的时间越短',
            default: 1.025,
            type: cc.Float
        },
        Decelerate: {
            tooltip: '减速度：此数值越大，停下来的时间越长',
            default: 0.875,
            type: cc.Float
        },
        StopValue: {
            tooltip: '减速时，速度降到此数值即停止',
            default: 128,
            type: cc.Integer
        },
        FallValue: {
            tooltip: '掉落最大限速',
            default: -1024,
            type: cc.Integer
        },
        FallGravity: {
            tooltip: '控制角色掉落时超过最大速度后的重力系数，此数值越大掉的越快',
            default: 0.75,
            type: cc.Float
        },
        MaxJumpTimes: {
            tooltip: '最多可以跳跃的次数',
            default: 1,
            type: cc.Integer
        },
        JumpTimes: {
            tooltip: '当前可以跳跃的次数',
            default: 1,
            type: cc.Integer
        },
        //控制按钮
        UpNode: cc.Node,
        LeftNode: cc.Node,
        RightNode: cc.Node,
        DirMask: cc.Node,
        //拍打动作
        RunFrames: [cc.SpriteFrame],
        //受伤动作
        HurtFrames: [cc.SpriteFrame],
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.PreDirection = Direction.None;
        this.CurDirection = Direction.None;
        this.SpeedUpTime = 0;
        this.isEnabled = true;
        this.JumpTimes = this.MaxJumpTimes;

        console.log('当前系统: ', cc.sys.os);
        console.log('当前平台：', cc.sys.platform);
        if(!cc.sys.isMobile) {
            cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyPressed, this);
            cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP, this.onKeyReleased, this);
        }

        this.DirMask.on(cc.Node.EventType.TOUCH_START, this.onDirPressed, this);
        this.DirMask.on(cc.Node.EventType.TOUCH_MOVE, this.onDirPressed, this);
        this.DirMask.on(cc.Node.EventType.TOUCH_CANCEL, this.onDirReleased, this);
        this.DirMask.on(cc.Node.EventType.TOUCH_END, this.onDirReleased, this);
        this.UpNode.on(cc.Node.EventType.TOUCH_START, this.onUpPressed, this);
        this.UpNode.on(cc.Node.EventType.TOUCH_CANCEL, this.onUpReleased, this);
        this.UpNode.on(cc.Node.EventType.TOUCH_END, this.onUpReleased, this);

        this.initRole();
    },

    onDestroy() {
        this.isEnabled = false;
    },

    checkAABB () {
        let aabb = this.RoleCollider.getAABB();
        let contactList = cc.director.getPhysicsManager().testAABB(aabb);
        // console.log('检测AABB', contactList.length, contactList);
        if(contactList.length <= 1) {
            //唯一的一个是自己
            this.Left = this.Right = this.Top = this.Bottom = false;
            // console.log('没有碰撞');
        } else {
            //先检测y轴方向
            let left = false, right = false, top = false, bottom = false;
            for(let i = 0; i < contactList.length; i++) {
                let contact = contactList[i];
                if(contact !== this.RoleCollider) {
                    let _aabb = contact.getAABB();
                    if(aabb.yMax >= _aabb.yMax) {
                        top = true;
                    } else if(aabb.yMin <= _aabb.yMin) {
                        bottom = true;
                    }
                }
            }
            //再检测x轴方向
            if(top || bottom) {
                for(let i = 0; i < contactList.length; i++) {
                    let contact = contactList[i];
                    if(contact !== this.RoleCollider) {
                        let _aabb = contact.getAABB();
                        if(aabb.xMax >= _aabb.xMax) {
                            left = true;
                        } else if(aabb.xMin <= _aabb.xMin) {
                            right = true;
                        }
                    }
                }
            }
            // console.log(`left: ${left}, right: ${right}, top: ${top}, bottom: ${bottom}`);
            this.Left = left;
            this.Right = right;
            this.Top = top;
            this.Bottom = bottom;
        }
    },

    initRole() {
        this.Role = cc.find('Canvas/Map/Role');
        this.RoleBody = this.Role.getComponent(cc.RigidBody);
        this.RoleCollider = this.Role.getComponent(cc.PhysicsPolygonCollider);
        this.RoleBody.enabledContactListener = true;
        this.RoleBody.onBeginContact = function(contact, self, other) {
            // console.log('onBeginContact: ', other.tag, contact, other);
            if(other.tag == 1001) {
                cc.director.loadScene('MainScene');
            }
        };
        this.Role.zIndex = 1;

        this.RoleAnimation = this.Role.addComponent(cc.Animation);
        let newClip = function(name, frames, interval, wrapMode) {
            let clip = cc.AnimationClip.createWithSpriteFrames(frames, interval);
            clip.name = name;
            clip.wrapMode = wrapMode;
            return clip;
        };
        
        this.RoleAnimation.addClip(newClip('run', this.RunFrames, 10, cc.WrapMode.Loop));
        this.RoleAnimation.addClip(newClip('die', this.HurtFrames, 10, cc.WrapMode.Normal));

        this.playDie = () => {
            this.RoleAnimation.play('die');
        };

        this.playHurt = () => {
            this.RoleAnimation.play('die');
            this.scheduleOnce(this.playRun, 1);
        }

        this.playRun = () => {
            this.RoleAnimation.play('run');
        }

        this.playRun();
    },

    onLeft() {
        if(!this.isEnabled) return;
        if(this.PreDirection == Direction.Left) {
            return;
        }
        this.CurDirection = Direction.Left;
        this.Role.scaleX = this.CurDirection;
    },

    onRight() {
        if(!this.isEnabled) return;
        if(this.PreDirection == Direction.Right) {
            return;
        }
        this.CurDirection = Direction.Right;
        this.Role.scaleX = this.CurDirection;
    },

    onJump() {
        if(!this.isEnabled) return;
        // console.log('可以跳跃的次数: ', this.JumpTimes);
        if(this.JumpTimes <= 0) {
            return;
        }
        --this.JumpTimes;
        
        this.RoleBody.gravityScale = 1;
        let speedX = this.RoleBody.linearVelocity.x;
        // this.RoleBody.linearVelocity = cc.v2(speedX, this.Speed.y);
        this.RoleBody.linearVelocity = cc.v2(0, 0);
        this.RoleBody.applyLinearImpulse(cc.v2(0, 2.05*this.Speed.y), this.RoleBody.getWorldCenter(), true);
        this.JumpDate = new Date();
    },

    onLeftRightReleased() {
        if(this.PreDirection == Direction.None) {
            return;
        }
        this.CurDirection = Direction.None;
    },

    //按下事件
    onKeyPressed: function (event) {
        let keyCode = event.keyCode;
        switch(keyCode) {
            case cc.macro.KEY.a:
            case cc.macro.KEY.left:
                this.onLeft();
                break;
            case cc.macro.KEY.d:
            case cc.macro.KEY.right:
                this.onRight();
                break;
            case cc.macro.KEY.w:
            case cc.macro.KEY.up:
                this.onJump();
                break;
        }
    },
    
    //松开事件
    onKeyReleased: function (event) {
        let keyCode = event.keyCode;
        switch(keyCode) {
            case cc.macro.KEY.a:
            case cc.macro.KEY.left:
            case cc.macro.KEY.d:
            case cc.macro.KEY.right:
                this.onLeftRightReleased();
                break;
        }
    },

    enable() {
        this.isEnabled = true;
    },

    disable() {
        this.isEnabled = false;
    },

    changeColor(node, pressed) {
        node.color = pressed ? cc.Color.BLACK : cc.Color.WHITE;
    },

    onDirPressed(event) {
        if(!this.isEnabled) return;
        let pos = event.getLocation();
        let isleft = pos.x <= 115;
        this.changeColor(this.LeftNode, isleft); 
        this.changeColor(this.RightNode, !isleft); 

        if(this.Role) {
            if(isleft) {
                this.onLeft();
            } else {
                this.onRight();
            }
        }
    },

    onDirReleased(event) {
        this.changeColor(this.LeftNode, false);
        this.changeColor(this.RightNode, false);
        this.onLeftRightReleased();
    },

    onUpPressed(event) {
        if(!this.isEnabled) return;
        this.changeColor(this.UpNode, true);
        this.onJump();
    },

    onUpReleased(event) {
        this.changeColor(this.UpNode, false);
    },

    checkJump() {
        if(this.Top && Math.abs(this.RoleBody.linearVelocity.y) <= 2 && this.JumpTimes < this.MaxJumpTimes) {
            console.log('重置跳跃次数', (new Date()).getTime() - this.JumpDate.getTime());
            this.JumpTimes = this.MaxJumpTimes;
        }
    },

    update (dt) {
        this.checkAABB();
        this.checkJump();
    },

    lateUpdate(dt) {
        let sx = this.RoleBody.linearVelocity.x;
        let sy = this.RoleBody.linearVelocity.y;

        if(this.CurDirection == Direction.None) {
            if(Math.abs(sx) > this.StopValue) {
                sx *= this.Decelerate;
            } else {
                sx = 0;
            }
            this.SpeedUpTime = 0;
            this.PreDirection = Direction.None;
        } else if(this.CurDirection == Direction.Left) {
            if(sx > -this.Speed.x) {
                this.SpeedUpTime += dt;
                sx = -Math.max(this.InitialSpeedX, Math.min(-sx*this.Accelerate, this.Speed.x));
            }
            this.PreDirection = Direction.Left;
        } else if(this.CurDirection == Direction.Right) {
            if(sx < this.Speed.x) {
                this.SpeedUpTime += dt;
                sx = Math.max(this.InitialSpeedX, Math.min(sx*this.Accelerate, this.Speed.x));
            }
            this.PreDirection = Direction.Right;
        }
        if(sy > this.Speed.y) {
            sy = this.Speed.y;
            // console.log('上行限速');
        } else if(sy < this.FallValue) {
            sy = this.FallValue;
            // console.log('下行限速');
        } else if(sy < -500) {
            this.RoleBody.gravityScale = this.FallGravity;
            // console.log('下行减重');
        } else if(sy < -36 && !this.Top) {
            let scale = Math.max(1.8, this.RoleBody.gravityScale);
            scale += 6 * dt;
            this.RoleBody.gravityScale = scale;
            // console.log('下行增速', sy, this.RoleBody.gravityScale);
        } else if(sy < -2 && !this.Top) {
            //滞空时间控制为2帧，大概0.15秒
            // console.log('滞空', sy);
            this.RoleBody.gravityScale = 0.8;
        }
        if(sx > this.Speed.x) {
            sx = this.Speed.x;
            // console.log('右行限速');
        } else if(sx < -this.Speed.x) {
            sx = - this.Speed.x;
            // console.log('左行限速');
        }
        this.RoleBody.linearVelocity = cc.v2(sx, sy);
    }
});
