// 位置管理器

cc.Class({
    extends: cc.Component,

    properties: {
        SeatViews: [cc.Node],
        SeatPrefab: cc.Prefab,
        FoodDisplayItem: cc.Prefab,
        TabViews: [cc.Node],
        DetailNode: cc.Node,
        DetailContainer: cc.Node,
        DetailCoinLab: cc.Label,
    },

    // LIFE-CYCLE CALLBACKS:
    onLoad() {
        this.registerEvents();
        this.initSeats();
    },

    onDestroy() {
        this.unregisterEvents();
    },

    start() {
        this.loadData();
        this.changeTab(Wu57.Constant.SeatTypes.Seat1);
    },

    //注册事件
    registerEvents() {
        Wu57.EventMgr.register(Wu57.Constant.EventCode.ClickTaskItem, this.onShowDetail.bind(this));
    },

    //销毁事件
    unregisterEvents() {
        Wu57.EventMgr.unregister(Wu57.Constant.EventCode.ClickTaskItem);
    },

    //初始化位置
    initSeats() {
        this._seats = [];
        let row = this.SeatViews.length;
        let col = 3;
        for (let i = 0; i < row; i++) {
            let view = this.SeatViews[i];
            for(let j = 0; j < col; j++) {
                let item = cc.instantiate(this.SeatPrefab);
                item.seat = i * col + j;
                item.magic = i + 1;
                view.getComponent(cc.ScrollView).content.addChild(item);
                this._seats.push(item);
            }
        }
    },
    
    //加载数据
    loadData() {
        let timetask = Wu57.DatabaseMgr.getTimeTask();
        console.log('[MainScene loadData]', timetask);
        for (let taskid in timetask) {
            let task = timetask[taskid];
            let seat = task.seat;
            let item = this._seats[seat];
            item.getComponent('FoodItem').setTask(task);
        }
    },

    //切换页签
    changeTab(tab) {
        for(let i=0; i<this.TabViews.length; i++) {
            let btn = this.TabViews[i].getComponent('TabButton');
            if(i+1 == tab) {
                btn.select();
            } else {
                btn.unselect();
            }

            let seat = this.SeatViews[i];
            seat.zIndex = i+1 == tab ? 1 : 0;
        }
    },

    //按钮点击事件统一处理
    onClicked(event, data) {
        event.stopPropagation();

        switch(data) {
            case 'Tab1':
            case 'Tab2':
            case 'Tab3':
            case 'Tab4':
                let tab = data.substr(-1);
                this.changeTab(tab);
                break;
            case 'Submit':
                this.submitTask(event.target);
                break;
        }
    },

    //显示任务详情
    onShowDetail(params) {
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.Click);
        this.DetailNode.opacity = 255;

        cc.find('Canvas').on(cc.Node.EventType.TOUCH_START, this.onHideDetail, this);
        let taskList = params.list;
        let taskCount = taskList.length;
        let items = this.DetailContainer.children;
        let allEnough = true;
        for(let i=0; i<items.length; i++) {
            let isEnable = i < taskCount;
            let item = items[i];
            item.active = isEnable;
            if(isEnable) {
                let [itemId, count] = taskList[i];
                let itemSprite = item.getChildByName('ItemSprite').getComponent(cc.Sprite);
                let nameLab = item.getChildByName('NameLab').getComponent(cc.Label);
                let numLab = item.getChildByName('NumLab').getComponent(cc.Label);
                let flagImg = item.getChildByName('Flag');
                itemSprite.spriteFrame = Wu57.ResMgr.getItemImage(itemId);
                let itemNum = Wu57.DatabaseMgr.getItemCountById(itemId);
                numLab.string = `${itemNum}/${count}`;
                nameLab.string = Wu57.ConfigMgr.getItemInfo(itemId).name;
                item.foodId = itemId;
                item.on(cc.Node.EventType.TOUCH_END, this.onRedirect, this);
                let enough = itemNum >= count;
                numLab.node.color = enough ? cc.Color.WHITE : cc.Color.RED;
                if(!enough) {
                    flagImg.stopAllActions();
                    flagImg.runAction(cc.repeatForever(
                        cc.sequence(
                            cc.fadeIn(0.75), 
                            cc.fadeOut(0.75)
                        )
                    ));
                    flagImg.active = true;
                    allEnough = false;
                } else {
                    flagImg.active = false;
                }
            }
        }

        let coin = params.reward[0][1];
        let submitBtn = this.DetailNode.getChildByName('SubmitBtn')
        submitBtn.active = allEnough;
        submitBtn.params = params;
        submitBtn.coin = coin;
        this.DetailCoinLab.string = coin;
    },

    onHideDetail(event) {
        let pos = event.getLocation();
        let box = this.DetailNode.getBoundingBoxToWorld();
        if(!box.contains(pos)) {
            this.hideDetail();
        }
    },

    hideDetail() {
        this.DetailNode.opacity = 0;
        cc.find('Canvas').off(cc.Node.EventType.TOUCH_START, this.onHideDetail, this);
    },

    onRedirect(event) {
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.Click);
        let target = event.target;
        let foodId = target.foodId;
        let config = Wu57.ConfigMgr.getFoodInfo(foodId);
        let magic = config.magic;
        this.changeTab(magic);

        if(this.firstItem) {
            this.firstItem.getComponent('FoodItem').hideFinger();
            this.firstItem = null;
        }

        let firstItem = null;
        let view = this.SeatViews[magic-1];
        let children = view.getComponent(cc.ScrollView).content.children;
        for(let i = 0; i < 3; i++) {
            let item = children[i];
            let empty = item.getComponent('FoodItem').isEmptySeat();
            if(empty && !firstItem) {
                firstItem = item;
                this.firstItem = firstItem;
            }
        }

        if(firstItem) {
            firstItem.getComponent('FoodItem').showFinger();
        } else {
            Wu57.NoticeMgr.sendTip('没有更多位置了！');
        }
    },

    submitTask(sender) {
        let self = this;
        let data = {
            removeItems: sender.params.list,
            coin: sender.coin,
            main: sender.params.type == 1//是否主线任务
        }
        Wu57.DatabaseMgr.delMainTask(sender.params.id, data, () => {
            Wu57.EventMgr.emit(Wu57.Constant.EventCode.SubmitTask);
            Wu57.NoticeMgr.sendTip(`获得金币+${data.coin}`);
            self.hideDetail();
        }, () => {
            Wu57.NoticeMgr.sendError('网络好像有点问题哦~');
        });
    }

});
