// 菜品制作列表

cc.Class({
    extends: cc.Component,

    properties: {
        MaskTouch: require('MaskTouch'),
        FoodPagePrefab: cc.Prefab,
        TitleLab: cc.Label,
        PageView: cc.PageView,
    },

    // LIFE-CYCLE CALLBACKS:

    onEnter(param) {
        this.node.active = true;
        this.node.scale = 0;
        this.MaskTouch.enableTouch();

        // console.log('FoodListUI onEnter: ', param);

        this.node.runAction(cc.scaleTo(0.5, 1).easing(cc.easeBackInOut()));
        this.reload(param);
    },

    onExit() {
        this.node.runAction(cc.scaleTo(0.5, 0).easing(cc.easeBackInOut()));
        this.scheduleOnce(() => {
            this.PageView.removeAllPages();
            this.MaskTouch.disableTouch();
            this.node.active = false;
        }, 0.6);
    },

    reload(param) {
        param.ui = this;
        let recipes = Wu57.DatabaseMgr.getRecipeList();
        let foodList = Wu57.ConfigMgr.getFoodListByMagicAndRecipe(param.magic, recipes);
        let foodCount = foodList.length;
        let pages = Math.ceil(foodCount / 4);
        for(let i=0; i<pages; i++) {
            let foodpage = cc.instantiate(this.FoodPagePrefab);
            this.PageView.addPage(foodpage);
            let pagecom = foodpage.getComponent('FoodPage');
            let list = [];
            for(let j=0; j<4; j++) {
                let index = i*4+j;
                if(index < foodCount) {
                    list.push(foodList[index]);
                }
            }
            pagecom.setPage(i, list, param);
        }
        this.PageView.setCurrentPageIndex(0);
    },

    onBtnCloseClicked() {
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.Click);

        this.onExit();
    }

});
