////////////////////////////////////////////////////////
// 数学操作基础库
////////////////////////////////////////////////////////

let MathUtil = {
    // 随机整数[min, max]
    randomInt(min, max) {
        return Math.floor(Math.random() * (max - min + 1) + min);
    },

    // 随机浮点数[min, max]
    randomFloat(min, max) {
        return Math.random() * (max - min + 1) + min;
    },

    // 整数高位补零
    prefixZero(num, n) {
        return (Array(n).join(0) + num).slice(-n);
    }
}

module.exports = MathUtil;
