// 菜品

const FoodState = cc.Enum({
    Unlock: 0,
    Empty: 1,
    Exist: 2,
});

cc.Class({
    extends: cc.Component,

    properties: {
        Container: [cc.Node],
        UnlockNode: cc.Node,
        EmptyNode: cc.Node,
        FoodNode: cc.Node,
        ItemSprite: cc.Sprite,
        ProBar: cc.ProgressBar,
        Clear: cc.Node,
        TimeLab: cc.Label,
        Finger: cc.Node,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this._taskid = null;
        this._taskdone = false;
        this.UnlockNode.on(cc.Node.EventType.TOUCH_END, this.onSeatUnlock, this);
        this.EmptyNode.on(cc.Node.EventType.TOUCH_END, this.onTaskAdd, this);
        this.ItemSprite.node.on(cc.Node.EventType.TOUCH_END, this.onTaskDel, this);
        this.reset();
    },

    //重置
    reset() {
        this.setState(FoodState.Empty);
    },

    //开始循环
    runLoop() {
        this.onRefresh();
        this.schedule(this.onRefresh, 1);
    },

    //停止循环
    stopLoop() {
        this.unschedule(this.onRefresh);
    },

    //刷新
    onRefresh() {
        if(this.FoodNode.active && this._taskid) {
            let task = Wu57.DatabaseMgr.getTimeTask()[this._taskid];
            --task.time;
            this.TimeLab.string = Wu57.UtilsMgr.TimeUtil.secondToHour(task.time);
            this.ProBar.progress = 1 - task.time / task.limit;
            if(task.time <= 0) {
                this._taskdone = true;
                this.ProBar.node.active = false;
                this.Clear.active = true;
                this.stopLoop();
            }
        }
    },

    //设置任务
    setTask(task) {
        this._taskid = task.taskid;
        this._taskdone = false;
        this.setState(FoodState.Exist);
        this.ItemSprite.spriteFrame = Wu57.ResMgr.getItemImage(task.food);
        this.Clear.active = false;
        this.ProBar.node.active = true;
        this.runLoop();
    },

    //设置位置状态
    setState (state) {
        for(let i=0; i<this.Container.length; i++) {
            this.Container[i].active = state == i;
        }
        this.state = state;
    },

    //获得位置状态
    getState() {
        return this.state;
    },

    //是否空位
    isEmptySeat() {
        return this.state == FoodState.Empty;
    },

    //添加任务
    onTaskAdd(event) {
        event.stopPropagation();
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.Click);
        Wu57.EventMgr.emit(Wu57.Constant.EventCode.ClickTimeTask, {
            magic: this.node.magic,
            seat: this.node.seat,
            target: this
        });
        this.hideFinger();
    },

    //完成任务
    onTaskDel(event) {
        event.stopPropagation();
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.Click);

        if(!this._taskdone) {
            Wu57.NoticeMgr.sendTip('任务未完成，先去看看别的吧');
            return;
        }

        let self = this;
        let remove = function() {
            let node = cc.instantiate(self.ItemSprite.node);
            let wp = self.node.convertToWorldSpaceAR(cc.v2(0,0));
            node.setPosition(wp);
            node.parent = cc.find('Canvas/MainUI');
            node.runAction(cc.sequence(
                cc.jumpBy(0.75, cc.v2(50, 120), 40, 2),
                cc.fadeOut(1),
                cc.removeSelf()
            ));

            Wu57.EventMgr.emit(Wu57.Constant.EventCode.TimeTaskDone);
        }
        
        Wu57.DatabaseMgr.delTimeTask(self._taskid, function onSuccess(res) {
            self._taskid = null;
            self._taskdone = false;
            self.stopLoop();
            self.setState(1);
            remove();
        }, function onFailed(res) {
            Wu57.NoticeMgr.checkNetwork();
        });
    },

    //解锁位置 TODO
    onSeatUnlock(event) {
        event.stopPropagation();
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.Click);
    },

    //显示手指
    showFinger() {
        this.Finger.active = true;
        this.Finger.runAction(cc.repeatForever(
            cc.sequence(
                cc.scaleTo(0.75, 0.8),
                cc.scaleTo(0.75, 1),
            )
        ));
    },

    //隐藏手指
    hideFinger() {
        this.Finger.stopAllActions();
        this.Finger.active = false;
    }
});
