// 

cc.Class({
    extends: cc.Component,

    properties: {
        BagNode: cc.Node,
        CoinLab: cc.Label,
        DiamondLab: cc.Label,
        TitleLab: cc.Label,
        NumLab: cc.Label,
        TipLab: cc.Label,
        EnlargeTipLab: cc.Label,

        Pageview: cc.PageView,
        Buttons: cc.Node,
        EnlargeBtn: cc.Node,

        SellUI: cc.Node,
        PageItem: cc.Prefab,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        Wu57.EventMgr.register(Wu57.Constant.EventCode.ClickBagItem, this.onClickBagItem.bind(this));
        this.current_selected_item = null;

        this.BagNode.on(cc.Node.EventType.TOUCH_START, () => {});
        this.BagNode.on(cc.Node.EventType.TOUCH_END, () => {});
        this.BagNode.on(cc.Node.EventType.TOUCH_MOVE, () => {});
        this.BagNode.on(cc.Node.EventType.TOUCH_CANCEL, () => {});
    },

    start () {
        this.runLoop();
        this.loadPages();
    },

    //点击道具事件
    onClickBagItem(params) {
        if(params.selected) {
            if(this.current_selected_item) {
                this.current_selected_item.unselected();
            } else {
                this.openButtons();
            }
            this.current_selected_item = params.sender;
        } else {
            this.current_selected_item.unselected();
            this.current_selected_item = null;
            this.closeButtons();
        }
    },

    //开启更新循环
    runLoop() {
        this.refresh();
        this.schedule(this.refresh, 1);
    },

    //终止更新循环
    stopLoop() {
        this.unschedule(this.refresh);
    },

    //更新数据
    refresh() {
        this.CoinLab.string = Wu57.DatabaseMgr.getCoin();
        this.DiamondLab.string = Wu57.DatabaseMgr.getDiamond();

        let space = Wu57.DatabaseMgr.getBagSpace();
        let current = Wu57.DatabaseMgr.getItemCount();
        this.NumLab.string = `材料：${current}/${space}`;
    },

    //加载有道具的页面
    loadPages() {
        let space = Wu57.DatabaseMgr.getBagSpace();
        for(let i=0; i<space/Wu57.Constant.BagMaxCountInPage; i++) {
            let page = cc.instantiate(this.PageItem);
            this.Pageview.insertPage(page, i);
            let com = page.getComponent('BagPage');
            com.reset();
            com.setPage(i+1);
        }
        this.refreshEnlargePage();
    },

    //刷新有道具的页面
    refreshPages() {
        let pages = this.Pageview.getPages();
        let space = Wu57.DatabaseMgr.getBagSpace();
        for(let i=0; i<space/Wu57.Constant.BagMaxCountInPage; i++) {
            let page = pages[i];
            let com = page.getComponent('BagPage');
            com.setPage(i+1);
        }
        this.refreshEnlargePage();
    },

    //清除所有页面
    clearAllPages() {
        let pages = this.Pageview.getPages();
        let space = Wu57.DatabaseMgr.getBagSpace();
        for(let i=0; i<space/Wu57.Constant.BagMaxCountInPage; i++) {
            let page = pages[i];
            let com = page.getComponent('BagPage');
            com.reset();
        }
    },

    //卖出道具（全部数量）
    sellAll() {
        if(!this.current_selected_item) {
            console.error('没有选中的道具，一定有什么错了！');
            return;
        }
        let itemInfo = this.current_selected_item.itemInfo;
        Wu57.DatabaseMgr.sellItem(itemInfo.id, itemInfo.num, () => {
            let cfg = Wu57.ConfigMgr.ItemConfig[itemInfo.id];
            let price = itemInfo.num * cfg.price;
            Wu57.NoticeMgr.sendTip(`获得金币x${price}`);
            this.current_selected_item = null;
            this.closeButtons();
            this.refreshPages();
        }, () => {
            Wu57.NoticeMgr.sendError('卖出失败');
        });
    },

    //打开卖出道具页面
    openSellUI() {
        this.SellUI.active = true;
        this.SellUI.getComponent('BagSellUI').setItem(this.current_selected_item.itemInfo.id, this.current_selected_item.itemInfo.num);
        this.current_selected_item.unselected();
        this.current_selected_item = null;
        this.closeButtons();
    },

    //刷新扩充页
    refreshEnlargePage() {
        let isPageCanEnlarge = this.Pageview.getPages().length <= Wu57.Constant.BagMaxPages;
        this.EnlargeBtn.active = isPageCanEnlarge;
        this.EnlargeTipLab.string = isPageCanEnlarge ? '现在还不能用哦~' : '不能再扩大了哦~';
    },

    //扩充背包空间
    enlargeBagSpace() {
        let page = this.Pageview.getCurrentPageIndex();
        let cost = Wu57.ConfigMgr.getBagEnlargeCost(page);
        Wu57.NoticeMgr.open('提示', `是否花费${cost}钻石扩充仓库？`, () => {
            if(Wu57.DatabaseMgr.getDiamond() < cost) {
                Wu57.NoticeMgr.sendTip('钻石不足');
                return;
            }
            Wu57.DatabaseMgr.modBagSpace(Wu57.Constant.BagMaxCountInPage, cost, () => {
                Wu57.NoticeMgr.sendTip('仓库已扩大');
                let pages = this.Pageview.getPages().length;
                let page = cc.instantiate(this.PageItem);
                this.Pageview.insertPage(page, pages-1);
                let com = page.getComponent('BagPage');
                com.reset();
                com.setPage(pages);
                this.refreshEnlargePage();
            }, () => {
                Wu57.NoticeMgr.sendTip('仓库扩充失败');
            });
        });
    },

    //打开出售按钮集合
    openButtons() {
        this.Buttons.runAction(cc.moveBy(0.3, cc.v2(0, -120)).easing(cc.easeBackInOut()));
    },

    //关闭出售按钮集合
    closeButtons() {
        this.Buttons.runAction(cc.moveBy(0.3, cc.v2(0, 120)).easing(cc.easeBackInOut()));
    },

    //按钮点击事件集合
    onButtonClicked (event, code) {
        event.stopPropagation();

        switch(code) {
            case 'Enlarge':
                this.enlargeBagSpace();
                break;
            
            case 'AddCoin':
                break;
            
            case 'AddDiamond':
                break;

            case 'Back':
                this.clearAllPages();
                cc.director.loadScene('MainScene');
                break;

            case 'SellAll':
                this.sellAll();
                break;

            case 'SellOne':
                this.openSellUI();
                break;
        }
    },
});
