
cc.Class({
    extends: cc.Component,

    properties: {
        Buttons: cc.Node,
        Register: cc.Node,
        Login: cc.Node,
        Reset: cc.Node,
        MaskTouch: require('MaskTouch'),
    },

    // LIFE-CYCLE CALLBACKS:
    onLoad() {
        this.MaskTouch.enableTouch();
        this.AnimateTime = 0.5;
        this.RegPos = cc.v2(0, cc.winSize.height*0.5+this.Register.height);
        this.LoginPos = cc.v2(0, cc.winSize.height*0.5+this.Login.height);
        this.ResetPos = cc.v2(0, cc.winSize.height*0.5+this.Reset.height);
        this.OriginPos = cc.v2(0, 0);
    },

    onDestroy() {
        this.unregisterEvents();
    },

    //注册事件
    registerEvents() {
        Wu57.EventMgr.register(Wu57.Constant.EventCode.LoginSuccess, (res) => {
            this.closeAll();
            Wu57.NoticeMgr.sendTip('欢迎回来，' + res.username);
        });
        Wu57.EventMgr.register(Wu57.Constant.EventCode.LoginFailed, (res) => {
            if(!this.Login.active) {
                this.closeAll();
                this.onRegOpen();
            }
        });
        Wu57.EventMgr.register(Wu57.Constant.EventCode.RegSuccess, (res) => {
            console.log('RegSuccess:', res);
        });
        Wu57.EventMgr.register(Wu57.Constant.EventCode.RegFailed, (res) => {
            console.log('RegFailed:', res);
            if(!this.Register.active) {
                this.closeAll();
                this.onRegOpen();
            }
        });
    },

    //注销事件
    unregisterEvents() {
        Wu57.EventMgr.unregister(Wu57.Constant.EventCode.LoginSuccess);
        Wu57.EventMgr.unregister(Wu57.Constant.EventCode.LoginFailed);
        Wu57.EventMgr.unregister(Wu57.Constant.EventCode.RegSuccess);
        Wu57.EventMgr.unregister(Wu57.Constant.EventCode.RegFailed);
    },

    //关闭其他界面
    closeAll() {
        this.Buttons.active = false;
        this.Register.active = false;
        this.Login.active = false;
        this.Reset.active = false;
    },

    //打开按钮面板
    openButtons() {
        this.Buttons.active = true;
        this.Register.active = false;
        this.Login.active = false;
        this.Reset.active = false;
        this.Buttons.scale = 0;
        let act = cc.scaleTo(0.3, 1).easing(cc.easeBackInOut());
        this.Buttons.runAction(act);
    },

    //游客登录
    onTourOpen() {
        let params = {
            username: Wu57.UtilsMgr.StringUtil.randomUsername(),
            password: Wu57.UtilsMgr.StringUtil.randomUserpass(),
            email: Wu57.UtilsMgr.StringUtil.randomMailaddr(),
        };
        Wu57.LoginMgr.register(params);
    },

    //打开注册面板
    onRegOpen() {
        this.Buttons.active = false;
        this.Register.active = true;
        this.Register.setPosition(this.RegPos);
        let act = cc.moveTo(this.AnimateTime, this.OriginPos).easing(cc.easeBackInOut());
        this.Register.runAction(act);
    },

    //打开登录面板
    onLoginOpen() {
        this.Buttons.active = false;
        this.Login.active = true;
        this.Login.setPosition(this.LoginPos);
        let act = cc.moveTo(this.AnimateTime, this.OriginPos).easing(cc.easeBackInOut());
        this.Login.runAction(act);
    },

    //打开重置面板
    onResetOpen() {
        this.Login.active = false;
        this.Reset.active = true;
        this.Reset.setPosition(this.ResetPos);
        let act = cc.moveTo(this.AnimateTime, this.OriginPos).easing(cc.easeBackInOut());
        this.Reset.runAction(act);
    },

    //关闭注册面板
    onRegClose() {
        let act = cc.moveTo(this.AnimateTime, this.RegPos).easing(cc.easeBackInOut());
        this.Register.runAction(act);
        this.scheduleOnce(() => {
            this.openButtons();
        }, this.AnimateTime);
    },

    //关闭登录面板
    onLoginClose() {
        let act = cc.moveTo(this.AnimateTime, this.LoginPos).easing(cc.easeBackInOut());
        this.Login.runAction(act);
        this.scheduleOnce(() => {
            this.openButtons();
        }, this.AnimateTime);
    },

    //关闭重置面板
    onResetClose() {
        let act = cc.moveTo(this.AnimateTime, this.ResetPos).easing(cc.easeBackInOut());
        this.Reset.runAction(act);
        this.scheduleOnce(() => {
            this.onLoginOpen();
        }, this.AnimateTime);
    },

    //确认注册
    onRegConfirm() {
        let _UserBox = this.Register.getChildByName('UserBox').getComponent(cc.EditBox);
        if(_UserBox.string.length < 3) {
            Wu57.NoticeMgr.sendWarning('账号名称长一点会更酷哦');
            return;
        }

        let _Password = this.Register.getChildByName('PassBox').getComponent(cc.EditBox);
        if(_Password.string.length < 6) {
            Wu57.NoticeMgr.sendWarning('密码长度要超过6位数');
            return;
        }

        let _MailBox = this.Register.getChildByName('MailBox').getComponent(cc.EditBox);
        if(_MailBox.string.length < 8) {
            Wu57.NoticeMgr.sendWarning('请检查您的邮箱地址');
            return;
        }

        //尝试注册账户
        let params = {
            username: _UserBox.string,
            password: _Password.string,
            email: _MailBox.string
        };
        Wu57.LoginMgr.register(params);
    },

    //确认登录
    onLoginConfirm() {
        let _UserBox = this.Login.getChildByName('UserBox').getComponent(cc.EditBox);
        if(_UserBox.string.length == 0) {
            Wu57.NoticeMgr.sendWarning('请输入账号');
            return;
        }

        let _Password = this.Login.getChildByName('PassBox').getComponent(cc.EditBox);
        if(_Password.string.length == 0) {
            Wu57.NoticeMgr.sendWarning('请输入密码');
            return;
        }

        //尝试注册账户
        let params = {
            username: _UserBox.string,
            password: _Password.string
        };
        Wu57.LoginMgr.login(params);
    },

    //确认重置
    onResetConfirm() {
        let _MailBox = this.Reset.getChildByName('MailBox').getComponent(cc.EditBox);
        if(_MailBox.string.length == 0) {
            Wu57.NoticeMgr.sendWarning('请输入邮箱');
            return;
        }

        let params = {
            email: _MailBox.string
        };
        Wu57.LoginMgr.resetPassword(params);
    },

    //随机用户名
    onRandomUsername() {
        let _UserBox = this.Register.getChildByName('UserBox').getComponent(cc.EditBox);
        _UserBox.string = Wu57.UtilsMgr.StringUtil.randomUsername();
    }
});
