
cc.Class({
    extends: cc.Component,

    properties: {
        Seat: cc.Node,
        Start: cc.Node,

        Icon: cc.Node,
        ProBar: cc.ProgressBar,
        TimeLab: cc.Label,
        FinishLab: cc.Label
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.node.on(cc.Node.EventType.TOUCH_END, this.onTap, this);
        this._taskdone = false;
        this._taskid = null;
    },

    run() {
        this.onRefresh();
        this.schedule(this.onRefresh, 1);
    },

    stop() {
        this.unschedule(this.onRefresh);
    },

    onRefresh() {
        if(this.Start.active && this._taskid) {
            let task = Wu57.DatabaseMgr.getTimeTask()[this._taskid];
            --task.time;
            this.TimeLab.string = Wu57.UtilsMgr.TimeUtil.secondToHour(task.time);
            this.ProBar.progress = 1 - task.time / task.limit;
            if(task.time <= 0) {
                this._taskdone = true;
                this.TimeLab.node.active = false;
                this.ProBar.node.active = false;
                this.FinishLab.node.active = true;
                this.stop();
            }
        }
    },

    setTask(task) {
        this._taskid = task.taskid;
        this._taskdone = false;
        this.Seat.active = false;
        this.Start.active = true;
        this.FinishLab.node.active = false;
        this.ProBar.node.active = true;
        this.TimeLab.node.active = true;
        this.run();
    },

    onTap() {
        let self = this;
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.Click);
        if(this.Seat.active) {
            //添加任务
            let time = Wu57.UtilsMgr.MathUtil.randomInt(10, 60);
            let seat = this.node.seat;
            // let taskid = Wu57.UtilsMgr.StringUtil.randomTaskId();
            let task = {
                taskid: 'task' + seat,
                limit: time,
                time: time,
                seat: seat
            };

            Wu57.DatabaseMgr.addTimeTask(task, function onSuccess(res) {
                self.setTask(task);
            }, function onFailed(res) {
                Wu57.NoticeMgr.checkNetwork();
            });
        } else {
            //完成任务
            if(!this._taskdone) {
                Wu57.NoticeMgr.sendTip('任务未完成，先去看看别的吧');
                return;
            }
            this.Seat.active = true;
            this.Start.active = false;
            Wu57.DatabaseMgr.delTimeTask(this._taskid, function onSuccess(res) {
                self._taskid = null;
                self._taskdone = false;
                self.Start.active = false;
                self.Seat.active = true;
            }, function onFailed(res) {
                Wu57.NoticeMgr.checkNetwork();
            });
        }
    },
});
