
cc.Class({
    extends: cc.Component,

    properties: {
        ItemAtlas: cc.SpriteAtlas,
    },

    getItemImage(itemId, big=false) {
        if(itemId >= 3000 && itemId < 4000) {
            itemId -= 1000;
        }
        let name = big ? `Item_big_${itemId}` : `Item_${itemId}`;
        return this.ItemAtlas.getSpriteFrame(name);
    }
});
