
cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    init () {

    },

    //注册账号
    register(params) {
        Wu57.Bomb.User.register(params).then(res => {
            console.log('注册成功: ', res);
            Wu57.NoticeMgr.sendTip('注册成功，正在自动登录');
            Wu57.EventMgr.emit(Wu57.Constant.EventCode.RegSuccess, res);
            this.login(params);
        }).catch(err => {
            console.log('注册失败: ', err);
            if(err.code == 201) {
                Wu57.NoticeMgr.sendError('数据不完整');
            } else if(err.code == 202) {
                Wu57.NoticeMgr.sendError('用户名已存在');
            } else if(err.code == 203) {
                Wu57.NoticeMgr.sendError('邮箱已注册');
            } else if(err.code == 301) {
                Wu57.NoticeMgr.sendError('邮件地址无效');
            } else {
                Wu57.NoticeMgr.sendError('注册失败，请检查注册信息');
            }
            Wu57.EventMgr.emit(Wu57.Constant.EventCode.RegFailed, err);
        });
    },

    //登录账号
    login(params, callback) {
        let onComplete = function() {
            if(callback) {
                callback();
            }
        };

        Wu57.Bomb.User.login(params.username, params.password).then(res => {
            console.log('[登录成功]');
            let createdAt = (new Date(res.createdAt)).getTime();
            let updatedAt = (new Date(res.updatedAt)).getTime();
            if(createdAt === updatedAt) {
                console.log('[初次注册，初始化数据表]');
            } else {
                console.log('[正在为您同步数据]');
                onComplete();
            }
            
            this.scheduleOnce( () => {
                Wu57.NoticeMgr.sendTip('正在为您同步数据');
                Wu57.DatabaseMgr.init(res.objectId, params.password);
            }, 1);
            Wu57.StorageMgr._setSession(params);
            Wu57.EventMgr.emit(Wu57.Constant.EventCode.LoginSuccess, res);
        }).catch(err => {
            console.log('[登录失败]', err);
            if(err.code == 101) {
                Wu57.NoticeMgr.sendError('账号或密码错误');
            } else {
                Wu57.NoticeMgr.sendError('登录失败');
            }
            Wu57.EventMgr.emit(Wu57.Constant.EventCode.LoginFailed, err);
        });
    },

    //重置密码
    resetPassword(params) {
        Bmob.requestPasswordReset(params).then(res => {
            console.log('[发送重置邮件成功]', res);
            Wu57.NoticeMgr.sendTip('邮件已发送到您的邮箱\n请您根据操作重置密码');
        }).catch(err => {
            console.log('[发送重置邮件失败]', err);
            Wu57.NoticeMgr.sendError(err.error);
        });
    }
});
