// 标签按钮

cc.Class({
    extends: cc.Component,

    properties: {
        SelectedFrame: cc.SpriteFrame,
        UnselectedFrame: cc.SpriteFrame,
        DisabledFrame: cc.SpriteFrame,

        Background: cc.Sprite,
    },

    // LIFE-CYCLE CALLBACKS:
    
    select() {
        this.Background.spriteFrame = this.SelectedFrame;
    },

    unselect() {
        this.Background.spriteFrame = this.UnselectedFrame;
    },

    disable() {
        this.Background.spriteFrame = this.DisabledFrame;
    }
});
