// 对象池管理器
const NodePool = require('NodePool');

cc.Class({
    extends: cc.Component,

    properties: {
        BreadPiecePool: {
            default: [],
            type: NodePool
        },

        BagItemPool: {
            default: [],
            type: NodePool
        }
    },

    // LIFE-CYCLE CALLBACKS:

    init() {
        for (let i = 0; i < this.BreadPiecePool.length; ++i) {
            this.BreadPiecePool[i].init();
        }
        for (let i = 0; i < this.BagItemPool.length; ++i) {
            this.BagItemPool[i].init();
        }
    },

    reset() {
        for (let i = 0; i < this.BreadPiecePool.length; ++i) {
            this.BreadPiecePool[i].reset();
        }
        for (let i = 0; i < this.BagItemPool.length; ++i) {
            this.BagItemPool[i].reset();
        }
    },

    requestBreadPiece(type = 0) {
        let thePool = this.BreadPiecePool[type];
        console.log(`[对象池请求:${type}, 容量:${thePool.idx+1}]`);
        let item = null;
        if (thePool.idx >= 0) {
            item = thePool.request();
        }
        return item;
    },

    returnBreadPiece(type = 0, obj) {
        let thePool = this.BreadPiecePool[type];
        if (thePool.idx < thePool.size) {
            thePool.return(obj);
        } else {
            console.log('回收对象到已满的池子，一定有问题！');
            return;
        }
        console.log(`[对象池回收:${type}, 容量:${thePool.idx+1}]`);
    },

    request(pool, type = 0) {
        let thePool = this[pool][type];
        console.log(`[对象池请求:${type}, 容量:${thePool.idx+1}]`);
        let item = null;
        if (thePool.idx >= 0) {
            item = thePool.request();
        }
        return item;
    },

    return(pool, type = 0, obj) {
        let thePool = this[pool][type];
        if (thePool.idx < thePool.size) {
            thePool.return(obj);
        } else {
            console.log('回收对象到已满的池子，一定有问题！');
            return;
        }
        console.log(`[对象池回收:${type}, 容量:${thePool.idx+1}]`);
    },
});
