cc.Class({
    extends: cc.Component,

    properties: {
        CoinLab: cc.Label,
        DiamondLab: cc.Label,
        EnergyLab: cc.Label,
        Seat: cc.Node,

        Left: require('Left'),
        Right: require('Right'),
        Top: require('Top'),
        Bottom: require('Bottom'),

        TaskList: require('TaskList'),

        FoodListUI: require('FoodListUI'),
        SubGameUI1: require('SubGameUI1'),
        SubGameUI2: require('SubGameUI2'),
        SubGameUI3: require('SubGameUI3'),
        SubGameUI4: require('SubGameUI4'),

        Tabs: [cc.Node],
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        Wu57.EventMgr.register(Wu57.Constant.EventCode.ClickTimeTask, this.openFoodListUI.bind(this));
    },

    onDestroy() {
        this.stopLoop();
        Wu57.EventMgr.unregister(Wu57.Constant.EventCode.ClickTimeTask);
    },

    start() {
        this.onEnter();
        this.runLoop();
    },

    //主界面进入
    onEnter() {
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.PlayBg1);
        this.Left.onEnter();
        this.Right.onEnter();
        this.Top.onEnter();
        this.Bottom.onEnter();
        this.TaskList.onEnter();
        this.onSeatEnter();
    },

    //主界面退出
    onExit() {
        this.Left.onExit();
        this.Right.onExit();
        this.Top.onExit();
        this.Bottom.onExit();
        this.TaskList.onExit();
        this.onSeatExit();
    },

    //Seat进入
    onSeatEnter() {
        let time = Wu57.Constant.UIEnterExitTime;
        this.Seat.setScale(0);
        let act = cc.scaleTo(time, 1).easing(cc.easeElasticOut());
        this.Seat.runAction(act);
    },

    //Seat退出
    onSeatExit() {
        let time = Wu57.Constant.UIEnterExitTime;
        let act = cc.scaleTo(time, 0).easing(cc.easeElasticOut());
        this.Seat.runAction(act);
    },

    //打开FoodListUI
    openFoodListUI(param) {
        this.FoodListUI.onEnter(param);
    },

    //开启更新循环
    runLoop() {
        this.refresh();
        this.schedule(this.refresh, 1);
    },

    //终止更新循环
    stopLoop() {
        this.unschedule(this.refresh);
    },

    //更新数据
    refresh() {
        this.CoinLab.string = Wu57.DatabaseMgr.getCoin();
        this.DiamondLab.string = Wu57.DatabaseMgr.getDiamond();
        this.EnergyLab.string = Wu57.DatabaseMgr.getEnergy();
    },

    //打开子游戏
    openSubGame(gameUI) {
        this.onExit();
        let com = this[gameUI];
        if (com) {
            com.onEnter();
        };
    },

    //打开背包
    openBagUI() {
        cc.director.loadScene('BagScene');
    },

    //主界面按钮点击事件统一处理
    onClicked(event, data) {
        console.log('[点击触发事件]', data);
        switch (data) {
            case 'AddCoin':
                break;
            case 'AddDiamond':
                break;
            case 'AddEnergy':
                break;
            case 'SubGameUI1':
            case 'SubGameUI2':
            case 'SubGameUI3':
            case 'SubGameUI4':
                this.openSubGame(data);
                break;
            case 'Setting':
                break;
            case 'Music':
                event.target.getComponent('SwitchMusic').switch();
                break;
            case 'Bag':
                this.openBagUI();
                break;
        }
    }
});
