
cc.Class({
    extends: cc.Component,

    properties: {
        Gravity: {
            default: -320,
            type: cc.Integer,
            tooltip: '重力'
        },
        Camera: cc.Node,
        Role: cc.Node,
        Mask: cc.Node,
        Map: cc.TiledMap,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.PlayBg1);
        this.initPhysics();
        this.loadMap();
    },

    initPhysics() {
        let _bit = cc.PhysicsManager.DrawBits;
        let _mgr = cc.director.getPhysicsManager();
        let debugBits = _bit.e_shapeBit; //_bit.e_aabbBit
        _mgr.enabled = true;
        _mgr.gravity = cc.v2(0, this.Gravity);
        _mgr.debugDrawFlags = debugBits;
    },
    
    loadMap() {
        console.log(this.Map);
        this.drawRegions();
        this.drawRevives();
        this.drawObjects();
        this.drawTreasures();
    },

    drawRegions() {
        let g_region = this.Map.getObjectGroup('region');
        g_region.node.setAnchorPoint(cc.v2(0,0));
        let widget = g_region.node.addComponent(cc.Widget);
        widget.isAlignBottom = true;
        widget.isAlignLeft = true;
        widget.left = 0;
        widget.bottom = 0;
        let regions = g_region.getObjects();
        let body = g_region.addComponent(cc.RigidBody);
        body.type = cc.RigidBodyType.Dynamic;
        body.fixedRotation = true;
        body.gravityScale = 0;
        body.linearVelocity = cc.v2(0, 0);

        console.log('regions:', regions.length);
        let g_region_height = g_region.node.height;
        let g_region_width = g_region.node.width;
        for(let i=0; i<regions.length; i++) {
            let region = regions[i];
            let collider = g_region.addComponent(cc.PhysicsBoxCollider);
            let off_x = region.offset.x + region.width * 0.5;
            let off_y = g_region_height - region.offset.y - region.height * 0.5;
            collider.tag = 1; //GW.getColliderTag(region.name);
            collider.friction = 0;
            collider.offset = cc.v2(off_x, off_y);
            collider.size = cc.size(region.width, region.height);
            collider.apply();
        }
        this.Map_Region_Body = body;
    },

    drawRevives() {
        this.ReviveList = {};
        let g_revive = this.Map.getObjectGroup('revive');
        let revives = g_revive.getObjects();
        let g_revive_width = g_revive.node.width;
        let g_revive_height = g_revive.node.height;
        for(let i=0; i<revives.length; i++) {
            let revive = revives[i];
            let off_x = revive.offset.x + revive.width * 0.5;
            let off_y = g_revive_height - revive.offset.y - revive.height;
            this.ReviveList[revive.name] = cc.v2(off_x, off_y);
        }
        console.log('复活点: ', this.ReviveList);
        this.Role.setPosition(this.ReviveList['1']);
    },

    drawObjects() {
        let object = this.Map.getObjectGroup('object');

    },

    drawTreasures() {
        let treasure = this.Map.getObjectGroup('treasure');

    },

    start () {
        this.Mask.runAction(
            cc.sequence(
                cc.delayTime(0.3),
                cc.fadeOut(0.7),
                cc.callFunc(() => {
                    cc.find('Canvas/GamePad').getComponent('GamePad').enable();
                })
            )
        );
    },


    update(dt) {
        if(this.Map_Region_Body) {
            this.Map_Region_Body.linearVelocity = cc.v2(0, 0);
        }
    },


    lateUpdate(dt) {
        // console.log(this.Role.x, this.Role.y);
        let pos = this.Role.getPosition();
        let center = cc.v2(cc.winSize.width*0.5, cc.winSize.height*0.5);
        let offset = cc.v2(0, 48*2);
        let camera_pos = pos.add(offset).sub(center);
        this.Camera.setPosition(camera_pos);

        
    }
});
