// 屏蔽触摸

cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.mask();
    },

    onDestroy() {
        this.unmask();
    },

    mask() {
        this.node.on(cc.Node.EventType.TOUCH_START, this.onTapped, this);
        this.node.on(cc.Node.EventType.TOUCH_CANCEL, this.onTapped, this);
        this.node.on(cc.Node.EventType.TOUCH_END, this.onTapped, this);
        this.node.on(cc.Node.EventType.TOUCH_MOVE, this.onTapped, this);
    },

    unmask() {
        this.node.off(cc.Node.EventType.TOUCH_START, this.onTapped, this);
        this.node.off(cc.Node.EventType.TOUCH_CANCEL, this.onTapped, this);
        this.node.off(cc.Node.EventType.TOUCH_END, this.onTapped, this);
        this.node.off(cc.Node.EventType.TOUCH_MOVE, this.onTapped, this);
    },

    onTapped(event) {
        console.log(this.node.name + ' mask touch');
        event.stopPropagation();
    },
});
