const TimeUtil = {
    
    //高位补零
    prefixInteger(num, length=2) {
        return (Array(length).join('0') + num).slice(-length);
    },

    //小时
    getHour(time) {
        time = Math.floor(time / 3600);
        return this.prefixInteger(time);
    },

    //分钟
    getMin(time) {
        time = Math.floor(time / 60 % 60);
        return this.prefixInteger(time);
    },

    //秒
    getSec(time) {
        time = Math.floor(time % 60);
        return this.prefixInteger(time);
    },

    //格式化
    formatTime(c, s, m, h) {
        if(c) {
            if(h) {
                return `${h}时${m}分${s}秒`;
            } else {
                return `${m}分${s}秒`;
            }
        } else {
            if(h) {
                return `${h}:${m}:${s}`;
            } else {
                return `${m}:${s}`;
            }
        }
    },

    //秒数转成时分秒
    secondToHour(time, chinese=false) {
        let h = this.getHour(time);
        let m = this.getMin(time);
        let s = this.getSec(time);
        return this.formatTime(chinese, s, m, h);
    },

    //秒数转成分秒
    secondToMin(time, chinese=false) {
        let m = this.getMin(time);
        let s = this.getSec(time);
        return this.formatTime(chinese, s, m);
    }
}

module.exports = TimeUtil;