// 菜品制作页

cc.Class({
    extends: cc.Component,

    properties: {
        ListItems: [cc.Node]
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {

    },

    setPage(page, list, param) {
        let count = list.length;
        for(let i=0; i<4; i++) {
            let listItem = this.ListItems[i];
            let isEnable = i < count;
            listItem.active = isEnable;
            if(isEnable) {
                let food = list[i];
                let item = listItem.getComponent('FoodListItem');
                item.setFood(food, param);
            }
        }
    }

    // update (dt) {},
});
