
cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    onLoad() {
        this._contianer = this.node.parent.getChildByName('Container');
        this._subgame1 = cc.find('Canvas/SubGameUI1').getComponent('SubGameUI1');
    },

    //监听碰撞
    onCollisionEnter (other) {
        let bread = other.node.parent.getComponent('BreadPiece');
        if(bread) {
            let index = bread.node.index;
            if(this._subgame1.onCheckBreadPiece(index)) {
                bread.disableCollider();
                bread.isrunning = false;
                bread.node.parent = this._contianer;
                let y = (this._contianer.childrenCount - 1) * 10;
                bread.node.x = 0;
                bread.node.y = y;
                this.node.y = 25 + y;
            }
        }
    },
});
