
cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    onLoad() {
        this._subgame3 = cc.find('Canvas/SubGameUI3').getComponent('SubGameUI3');
    },

    //监听碰撞
    onCollisionEnter (other) {
        if(other.tag > 0) {
            let ani = other.node.getChildByName('HitAni');
            ani.active = true;
            ani.getComponent(cc.Animation).play();
            this._subgame3.mouseHit(other.node, other.tag);
        }
    },
});
