// 右

cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        let x = cc.winSize.width + this.node.width * 0.5;
        let y = cc.winSize.height;
        this.node.setPosition(x, y);
    },

    onEnter() {
        let offset = cc.v2(-this.node.width, 0);
        this.node.runAction( cc.moveBy(Wu57.Constant.UIEnterExitTime, offset).easing(cc.easeBackInOut()) );
    },

    onExit() {
        let offset = cc.v2(this.node.width, 0);
        this.node.runAction( cc.moveBy(Wu57.Constant.UIEnterExitTime, offset).easing(cc.easeBackInOut()) );
    },

    // update (dt) {},
});
