

cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.BagEnlargeConfig = require('BagEnlargeConfig');
        this.FoodConfig = require('FoodConfig');
        this.ItemConfig = require('ItemConfig');
        this.ItemTypeConfig = require('ItemTypeConfig');
        this.MagicTypeConfig = require('MagicTypeConfig');
        this.MouseConfig = require('MouseConfig');
        this.MouseLevelConfig = require('MouseLevelConfig');
        this.TaskConfig = require('TaskConfig');
    },

    getItemInfo(itemId) {
        return this.ItemConfig[itemId];
    },

    getBagEnlargeCost(page) {
        return this.BagEnlargeConfig[page-1].cost;
    },

    getTaskInfo(taskId) {
        return this.TaskConfig[taskId];
    },

    getFoodInfo(foodId) {
        return this.FoodConfig[foodId];
    },

    getFoodListByMagic(magic) {
        let list = [];
        for(let foodId in this.FoodConfig) {
            let cfg = this.FoodConfig[foodId];
            if(cfg.magic == magic) {
                list.push(cfg);
            }
        }
        return list;
    },

    getFoodListByMagicAndRecipe(magic, recipes) {
        let _recipes = new Set(recipes);
        let list = [];
        for(let foodId in this.FoodConfig) {
            let cfg = this.FoodConfig[foodId];
            if(cfg.magic == magic && _recipes.has(''+cfg.recipe)) {
                list.push(cfg);
            }
        }
        return list;
    },

    getMouseInfo(id) {
        return this.MouseConfig[id];
    },

    getMouseLevelInfo(level) {
        return this.MouseLevelConfig[level];
    }
});
