// 流程图
// 检查更新

cc.Class({
    extends: cc.Component,

    properties: {
        Release: false,
        MaskTouch: require('MaskTouch'),
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        cc.game.addPersistRootNode(this.node);
        this.node.zIndex = 99;
    },

    start() {
        this.initModules();
        this.listen();
        this.test();
    },

    test() {
        Wu57.NetMgr.requestGet('timestamp');
    },

    //是否发布版本
    isReleaseVersion() {
        return this.Release;
    },

    //显示遮罩层
    showMask() {
        this.MaskTouch.show();
    },

    //隐藏遮罩层
    hideMask() {
        this.MaskTouch.hide();
    },

    //初始化模块
    initModules() {
        window.Wu57 = {};

        Wu57.Root = this;
        Wu57.Constant = require('Constant');
        Wu57.CipherMgr = require('Cipher');
        Wu57.AdsMgr = this.node.getComponent('AdsMgr');
        Wu57.AudioMgr = this.node.getComponent('AudioMgr');
        Wu57.ConfigMgr = this.node.getComponent('ConfigMgr');
        Wu57.DatabaseMgr = this.node.getComponent('DatabaseMgr');
        Wu57.EventMgr = this.node.getComponent('EventMgr');
        Wu57.LoginMgr = this.node.getComponent('LoginMgr');
        Wu57.MapMgr = this.node.getComponent('MapMgr');
        Wu57.NetMgr = this.node.getComponent('NetMgr');
        Wu57.NoticeMgr = this.node.getComponent('NoticeMgr');
        Wu57.PoolMgr = this.node.getComponent('PoolMgr');
        Wu57.RedMgr = this.node.getComponent('RedMgr');
        Wu57.ResMgr = this.node.getComponent('ResMgr');
        Wu57.RoleMgr = this.node.getComponent('RoleMgr');
        Wu57.SceneMgr = this.node.getComponent('SceneMgr');
        Wu57.ScheduleMgr = this.node.getComponent('ScheduleMgr');
        Wu57.ShareMgr = this.node.getComponent('ShareMgr');
        Wu57.StorageMgr = this.node.getComponent('StorageMgr');
        Wu57.UpdateMgr = this.node.getComponent('UpdateMgr');
        Wu57.UtilsMgr = this.node.getComponent('UtilsMgr');

        let _B = Wu57.Constant.Bomb;
        Wu57.Bomb = require('Bomb');
        Wu57.Bomb.initialize(_B.Secret, _B.SafeKey);
        
        Wu57.StorageMgr.init();
        Wu57.NetMgr.init();
        Wu57.UpdateMgr.init();
        Wu57.LoginMgr.init();
        Wu57.PoolMgr.init();
        
        console.log(Wu57);
        this.onModuleLoaded();
    },

    //监听系统事件
    listen() {
        Wu57.EventMgr.register(Wu57.Constant.EventCode.DatabaseLoaded, () => {
            this.scheduleOnce(() => {
                Wu57.NoticeMgr.closeTip();
                Wu57.NoticeMgr.sendTip('客户端初始化成功，祝您游戏愉快', 1);
            }, 1);
            this.scheduleOnce( () => {
                Wu57.EventMgr.unregister(Wu57.Constant.EventCode.DatabaseLoaded);
                cc.director.loadScene('MainScene');
            }, 2);
        });

        cc.game.on(cc.game.EVENT_HIDE, function () {
            Wu57.DatabaseMgr.saveLastTime();
            Wu57.AudioMgr.pauseMusic();
            console.log('[游戏退到后台，保存数据]');
        });

        cc.game.on(cc.game.EVENT_SHOW, function () {
            console.log('[游戏回到前台，更新数据]');
            Wu57.AudioMgr.resumeMusic();
            Wu57.DatabaseMgr.setLastTime();
        });
    },

    //模块加载完成
    onModuleLoaded() {
        let _IndexScene = cc.find('Canvas').getComponent('IndexScene');
        _IndexScene.checkUpdate();

        if(cc.sys.platform == cc.sys.WECHAT_GAME) {
            wx.getSystemInfo({
                success (res) {
                    Wu57.SystemInfo = res;
                    console.log(Wu57.SystemInfo);
                }
            });
        }
    }

});
