// 任务列表

cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.TaskList = [];
        let taskItems = this.node.children;
        for(let i=0; i<taskItems.length; i++) {
            let item = taskItems[i].getComponent('TaskItem');
            item.reset();
            this.TaskList.push(item);
        }

        Wu57.EventMgr.register(Wu57.Constant.EventCode.SubmitTask, this.loadTaskList.bind(this));
        Wu57.EventMgr.register(Wu57.Constant.EventCode.TimeTaskDone, this.loadTaskList.bind(this));
    },

    onDestroy() {
        Wu57.EventMgr.unregister(Wu57.Constant.EventCode.SubmitTask);
        Wu57.EventMgr.unregister(Wu57.Constant.EventCode.TimeTaskDone);
    },

    onEnter() {
        let offset = this.node.width;
        this.node.x = -320-offset;
        this.node.runAction(cc.moveBy(0.3, offset, 0).easing(cc.easeBackInOut()));
    },

    onExit() {
        let offset = this.node.width;
        this.node.runAction(cc.moveBy(0.3, -offset, 0).easing(cc.easeBackInOut()));
    },

    start () {
        this.loadTaskList();
    },

    loadTaskList() {
        let mainTasks = Wu57.DatabaseMgr.getMainTask();
        // console.log('loadTaskList: ', mainTasks);

        let main = null;
        let taskCount = 0;
        let hasMainTask = false;
        let _mainTask = {list: []};

        let hasChanged = false;
        if(mainTasks) {
            let taskList = mainTasks.list;
            main = mainTasks.main;
            taskCount = taskList.length;
            for(let i=0; i<taskCount; i++) {
                let taskId = taskList[i];
                _mainTask.list.push(taskId);
                this.TaskList[i].setTaskId(taskId);
                if(taskId >= 10001 && taskId <= 19999) {
                    hasMainTask = true;
                    _mainTask.main = taskId;
                }
            }
        }
        
        if(taskCount < 4) {
            if(!hasMainTask && (!main)) {
                let taskId = 10001;
                _mainTask.main = taskId;
                _mainTask.list.push(taskId);
                taskCount++;
                hasChanged = true;
                hasMainTask = true;
            }
    
            if(taskCount < 4) {
                for(let i=taskCount; i<4; i++) {
                    let taskId = Wu57.UtilsMgr.MathUtil.randomInt(20001, 20051);
                    _mainTask.list.push(taskId);
                    taskCount++;
                }
                hasChanged = true;
            }
        }

        if(hasChanged) {
            // console.log('======taskLit Changed: ', _mainTask);
            let self = this;
            Wu57.DatabaseMgr.setMainTask(_mainTask, () => {
                for(let i=0; i<_mainTask.list.length; i++) {
                    let taskId = _mainTask.list[i];
                    self.TaskList[i].setTaskId(taskId);
                }
            }, () => {
                Wu57.NoticeMgr.open('问题', "任务列表加载失败，请重试", () => {
                    self.loadTaskList();
                }, () => {
                    self.loadTaskList();
                })
            })
        }

    }

    // update (dt) {},
});
