const Constant = require('Constant');

cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    //是否有存储项目
    hasItem(key) {
        return cc.sys.localStorage.getItem(key) != undefined;
    },

    //获得一个存储项目
    _getItem(key) {
        let value = cc.sys.localStorage.getItem(key);
        if (value) {
            if(Wu57.Root.isReleaseVersion()) {
                value = Wu57.CipherMgr.decode(value);
            }
            return value;
        }
        return undefined;
    },

    //设置一个存储项目
    _setItem(key, value) {
        if(Wu57.Root.isReleaseVersion()) {
            value = Wu57.CipherMgr.encode(value);
        }
        cc.sys.localStorage.setItem(key, value);
    },

    //获取加密会话
    _getSession() {
        let session = this._getItem('session');
        if(session) {
            session = Wu57.CipherMgr.decode(session);
            return JSON.parse(session);
        }
        return undefined;
    },

    //设置加密会话
    _setSession(session) {
        session = JSON.stringify(session);
        session = Wu57.CipherMgr.encode(session);
        this._setItem('session', session);
    },

    //初始化
    init() {
        let item1 = Constant.getStorageKey(Constant.StorageKey.Volume);
        if(!this.hasItem(item1)) {
            this.setVolume(Constant.Default.Volume);
        }

        let item2 = Constant.getStorageKey(Constant.StorageKey.VerisonCode);
        if(!this.hasItem(item2)) {
            this.setVersionCode(Constant.Default.VersionCode);
        }

        let item3 = Constant.getStorageKey(Constant.StorageKey.MusicOpen) 
        if(!this.hasItem(item3)) {
            this.setMusicOpen(true);
        }
    },

    //删除一个存储项目
    remove(key) {
        cc.sys.localStorage.removeItem(key);
    },

    //清除所有存储项目
    clear() {
        cc.sys.localStorage.clear();
    },

    //----------------------------------------------------------------
    
    //获得当前版本号
    getVersionCode() {
        let item = Constant.getStorageKey(Constant.StorageKey.VersionCode);
        let data = this._getItem(item);
        if(data) {
            return JSON.parse(data).value;
        }
        return Constant.Default.VersionCode;
    },

    //设置当前版本号
    setVersionCode(code) {
        let item = Constant.getStorageKey(Constant.StorageKey.VersionCode);
        let data = {value: code};
        this._setItem(item, JSON.stringify(data));
    },

    //获得上次时间
    getLastTime() {
        let item = Constant.getStorageKey(Constant.StorageKey.LastTime);
        let data = this._getItem(item);
        if(data) {
            return JSON.parse(data).value;
        }
        return Constant.StorageKey.Volume;
    },

    //设置上次时间
    setLastTime(time) {
        let item = Constant.getStorageKey(Constant.StorageKey.LastTime);
        let data = {value: time};
        this._setItem(item, JSON.stringify(data));
    },

    //获得当前音量
    getVolume() {
        let item = Constant.getStorageKey(Constant.StorageKey.Volume);
        let data = this._getItem(item);
        if(data) {
            return JSON.parse(data).value;
        }
        return Constant.StorageKey.Volume;
    },

    //设置当前音量
    setVolume(vol) {
        let item = Constant.getStorageKey(Constant.StorageKey.Volume);
        let data = {value: vol};
        this._setItem(item, JSON.stringify(data));
    },

    //获得当前关卡
    getCurrentLevel() {
        let item = Constant.getStorageKey(Constant.StorageKey.CurrentLevel);
        let data = this._getItem(item);
        if(data) {
            return JSON.parse(data).value;
        }
        return Constant.Default.CurrentLevel;
    },

    //设置当前关卡
    setCurrentLevel(level) {
        let item = Constant.getStorageKey(Constant.StorageKey.CurrentLevel);
        let data = {value: level};
        this._setItem(item, JSON.stringify(data));
    },

    //获取货币数量
    getCurrency() {
        let item = Constant.getStorageKey(Constant.StorageKey.Currency);
        let data = this._getItem(item);
        if(data) {
            return JSON.parse(data);
        }
        return {
            coin: 0,
            energy: 100
        };
    },

    //设置货币数量
    setCurrency(data) {
        let item = Constant.getStorageKey(Constant.StorageKey.Currency);
        this._setItem(item, JSON.stringify(data));
    },

    //获得关卡数据
    getLevelData() {
        let item = Constant.getStorageKey(Constant.StorageKey.LevelData);
        let data = this._getItem(item);
        if (data) {
            return JSON.parse(data);
        }
        return {};
    },

    //设置关卡数据
    setLevelData(data) {
        let item = Constant.getStorageKey(Constant.StorageKey.LevelData);
        this._setItem(item, JSON.stringify(data));
    },

    //设置静音
    setMusicOpen(open) {
        let item = Constant.getStorageKey(Constant.StorageKey.MusicOpen);
        this._setItem(item, JSON.stringify({value: open}));
    },

    //是否静音
    isMusicOpen() {
        let item = Constant.getStorageKey(Constant.StorageKey.MusicOpen);
        let data = this._getItem(item);
        if(data) {
            return JSON.parse(data).value;
        }
        return false;
    },
});
