// 

cc.Class({
    extends: cc.Component,

    properties: {
        Line: [cc.Node],
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.reset();
    },

    reset () {
        for(let i = 0; i < this.Line.length; i++) {
            let line = this.Line[i];
            this.removeLineItems(line);
        }
    },

    removeLineItems(line) {
        let children = line.children;
        let number = children.length;
        for(let i = number-1; i >= 0; i--) {
            let item = children[i];
            Wu57.PoolMgr.return('BagItemPool', 0, item);
        }
    },

    setPage(page) {
        this.page = page;
        this.reset();
        this.refreshItems();
    },

    addItem(itemInfo, line) {
        let bagItem = Wu57.PoolMgr.request('BagItemPool');
        bagItem.parent = this.Line[line];
        let com = bagItem.getComponent('BagItem');
        com.setItem(itemInfo);
    },

    refreshItems() {
        let itemArr = Wu57.DatabaseMgr.getItemList();
        let from = (this.page - 1) * 12;
        let to = from + 12;
        for(let i=from; i<to; i++) {
            let item = itemArr[i];
            if(!item) {
                break;
            }
            let line = Math.floor(i / 4) % 3;
            this.addItem(item, line);
        }
    }
});
