cc.Class({
    extends: cc.Component,

    properties: {
        Bg: cc.Node,
        StartMask: cc.Node,
        BigContainer: cc.Node,
        TargetContainer: cc.Node,
        DropContainer: cc.Node,
        PlateNode: cc.Node,
        ColliderArea: cc.Node,
        Interval: {
            type: cc.Float,
            default: 1.25,
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_END, this.onOutsideTap, this);
        this.Bg.on(cc.Node.EventType.TOUCH_START, this.onBgTap, this);
        this.Bg.on(cc.Node.EventType.TOUCH_END, this.onBgTap, this);
        this.Bg.on(cc.Node.EventType.TOUCH_CANCEL, this.onBgTap, this);

        this.PlateNode.on(cc.Node.EventType.TOUCH_START, this.onPlateMoved, this);
        this.PlateNode.on(cc.Node.EventType.TOUCH_MOVE, this.onPlateMoved, this);
        this.PlateNode.on(cc.Node.EventType.TOUCH_END, this.onPlateMoved, this);
        this.PlateNode.on(cc.Node.EventType.TOUCH_CANCEL, this.onBgTap, this);
    },

    //开启碰撞监听
    enableCollision() {
        let manager = cc.director.getCollisionManager();
        manager.enabled = true;
        if (!Wu57.Root.isReleaseVersion()) {
            manager.enabledDebugDraw = true;
            manager.enabledDrawBoundingBox = true;
        }
    },

    //关闭碰撞监听
    disableCollision() {
        let manager = cc.director.getCollisionManager();
        manager.enabled = false;
        manager.enabledDebugDraw = false;
        manager.enabledDrawBoundingBox = false;
    },

    //点击到背景图外部
    onOutsideTap() {
        this.onPause();
        Wu57.NoticeMgr.open('提示', '真的要离开吗？', () => {
            this.onExit();
        }, () => {
            this.onResume();
        });
    },

    //点击到背景图（不响应）
    onBgTap(event) {
        event.stopPropagation();
    },

    //进入
    onEnter() {
        Wu57.AudioMgr.play(Wu57.AudioMgr.AudioName.MainBg1);

        this.node.active = true;
        this.node.setContentSize(cc.winSize);
        this.node.setPosition(0, cc.winSize.height);
        let act = cc.moveTo(Wu57.Constant.UIEnterExitTime, cc.v2(0, 0)).easing(cc.easeBackInOut());
        this.node.runAction(act);

        this.StartMask.active = true;
        this.enableCollision();
        this.generateTargetBreadPiece();
    },

    //退出
    onExit() {
        this.clear();
        this.disableCollision();

        let a2 = cc.moveTo(Wu57.Constant.UIEnterExitTime, cc.v2(0, cc.winSize.height));
        let a3 = cc.callFunc(() => {
            cc.find('Canvas').getComponent('MainScene').onEnter();
            this.PlateNode.x = 0;
            this.PlateNode.getChildByName('ColliderArea').y = 25;
            this.node.active = false;
        });
        let act = cc.sequence(a2, a3).easing(cc.easeBackInOut());
        this.node.runAction(act);
    },

    //设置breadpiece的运行状态
    applyRunningState(isrunning) {
        let c1 = this.DropContainer.children;
        let s1 = c1.length;
        if (s1 > 0) {
            for (let i = s1 - 1; i >= 0; i--) {
                c1[i].getComponent('BreadPiece').isrunning = isrunning;
            }
        }
    },

    //暂停下落
    onPause() {
        this.isrunning = false;
        this.applyRunningState(false);
    },

    //恢复下落
    onResume() {
        this.isrunning = true;
        this.applyRunningState(true);
    },

    //底盘移动
    onPlateMoved(event) {
        event.stopPropagation();

        let offset = this.BigContainer.width * 0.5 - this.PlateNode.width;
        let current = event.getLocation();
        let locate = this.BigContainer.convertToNodeSpaceAR(current);
        if (locate.x <= -offset) {
            locate.x = -offset;
        } else if (locate.x >= offset) {
            locate.x = offset;
        }
        this.PlateNode.x = locate.x;
    },

    //清理容器
    cleanContainer(container) {
        let c1 = container.children;
        let s1 = c1.length;
        if (s1 > 0) {
            for (let i = s1 - 1; i >= 0; i--) {
                c1[i].getComponent('BreadPiece').recycle();
            }
            if (container.children.length > 0) {
                console.log('清理失败', container);
            }
        }
    },

    //清理对象池
    clear() {
        this.stopLoop();
        this.cleanContainer(this.TargetContainer);
        this.cleanContainer(this.DropContainer);
        let container = this.PlateNode.getChildByName('Container');
        this.cleanContainer(container);
    },

    //添加到目标容器
    addBreadToTargetContainer(index) {
        let bread = Wu57.PoolMgr.requestBreadPiece(0);
        this.TargetContainer.addChild(bread);
        bread.x = 0;
        let com = bread.getComponent('BreadPiece');
        com.setPiece(index - 1);
        com.unmask();
        com.disableCollider();
        this._breadpiece.push(index);
    },

    //生产到目标容器
    generateTargetBreadPiece() {
        this._breadpiece = [];
        this._checkIndex = 0;
        let math = Wu57.UtilsMgr.MathUtil;
        let size = math.randomInt(3, 5);
        for (let i = 0; i < size; i++) {
            let index = math.randomInt(2, 7);
            this.addBreadToTargetContainer(index);
        }
        this.addBreadToTargetContainer(1); //最后封顶

        console.log('BreadPieceList: ', this._breadpiece);
    },

    //生产到掉落容器
    generateDropBreadPiece() {
        if (!this.isrunning) return;

        let bread = Wu57.PoolMgr.requestBreadPiece(0);
        if (bread) {
            this.DropContainer.addChild(bread);
            let com = bread.getComponent('BreadPiece');
            com.init();
        }
    },

    //启动循环
    runLoop() {
        this.isrunning = true;
        this.generateDropBreadPiece();
        this.schedule(this.generateDropBreadPiece, this.Interval);
    },

    //停止循环
    stopLoop() {
        this.isrunning = false;
        this.unschedule(this.generateDropBreadPiece);
    },

    //开始
    onStartBtnClicked() {
        this.StartMask.active = false;
        this.runLoop();
    },

    //检测碰撞
    onCheckBreadPiece(index) {
        let top = this._breadpiece[this._checkIndex];
        let iscross = top == index;
        console.log('[检测碰撞]', top, index, iscross);
        if (top == index) {
            let target = this.TargetContainer.children[this._checkIndex];
            target.getComponent('BreadPiece').maskRight();
            this._checkIndex++;
        } else {
            let target = this.TargetContainer.children[this._checkIndex];
            target.getComponent('BreadPiece').maskWrong();
            this.onPause();
            Wu57.NoticeMgr.open('失败', '研究失败！', () => {
                this.onExit();
            }, () => {
                this.onExit();
            })
        }

        if (this._checkIndex == this._breadpiece.length) {
            this.onPause();
            Wu57.NoticeMgr.open('胜利', '研究成功！', () => {
                this.onExit();
            }, () => {
                this.onExit();
            });
        }

        return iscross;
    }
});
