// 砖块

cc.Class({
    extends: cc.Component,

    properties: {
        NormalFrames: [cc.SpriteFrame],
        ClickFrames: [cc.SpriteFrame],
        Brick: cc.Sprite,
        Order: cc.Label,
        Success: cc.Node,
        Failed: cc.Node,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.node.on(cc.Node.EventType.TOUCH_END, this.onClick, this);
        this.Order.node.active = false;
        this.Success.active = false;
        this.Failed.active = false;
    },

    attach(manager) {
        this.manager = manager;
        this.flip();
    },

    onClick() {
        if(this.manager && this.manager.isrunning) {
            if(!this.manager.isguide) {
                this.manager.click_step += 1;
                this.click();
                this.check();
            }
        }
    },

    click() {
        this.Brick.spriteFrame = this.ClickFrames[0];
        this.node.runAction(cc.sequence(
            cc.delayTime(0.3),
            cc.callFunc(() => {
                this.Brick.spriteFrame = this.NormalFrames[0];
            })
        ));
    },

    check() {
        let index = this.node.index;
        let round = this.manager.task.rounds[this.manager.current_round];
        let steps = this.manager.getCurrentSteps();
        let step = steps[this.manager.click_step-1];
        let isRight = step == index;
        if(isRight) {
            console.log('正确');
        } else {
            console.log('错误');
            this.showFail(round.fail);
            this.manager.addTotalTime(round.fail);
        }
        this.hideOrder();
        this.manager.check_list.push(isRight ? 1 : -1);

        if(this.manager.click_step == steps.length) {
            console.log('一轮检测结束############');
            let checkInfo = this.manager.getCheckInfo();
            if(checkInfo.wrong == 0) {
                this.showSuccess(round.success);
                this.manager.addTotalTime(round.success);
            }
            this.manager.isrunning = false;
            this.scheduleOnce(() => {
                this.manager.check_list = [];
                this.manager.next();
            }, 1);
        }
    },

    flip() {
        if(this.manager) {
            this.Brick.spriteFrame = this.NormalFrames[0];
            this.hideOrder();
        }
    },

    showOrder(order) {
        this.Order.node.active = true;
        this.Order.string = order;
    },

    hideOrder() {
        this.Order.node.active = false;
    },

    showSuccess(val) {
        this.Success.active = true;
        let text = this.Success.getChildByName('Text').getComponent(cc.Label);
        text.string = `+${val}s`;
        this.Success.runAction(cc.sequence(
            cc.spawn(
                cc.moveBy(1, cc.v2(0, 88)),
                cc.fadeIn(1)
            ),
            cc.callFunc(() => {
                text.string = '';
                this.hideSuccess();
            })
        ));
    },

    hideSuccess() {
        this.Success.y = 0;
        this.Success.active = false;
    },

    showFail(val) {
        this.Failed.active = true;
        let text = this.Failed.getChildByName('Text').getComponent(cc.Label);
        text.string = `${val}s`;
        this.Failed.runAction(cc.sequence(
            cc.spawn(
                cc.moveBy(1, cc.v2(0, 88)),
                cc.fadeIn(1)
            ),
            cc.callFunc(() => {
                text.string = '';
                this.hideFail();
            })
        ));
    },

    hideFail() {
        this.Failed.active = false;
    }
});
